.menu{
  padding-bottom: 1.25em;
}

.menu-item{
  width: 100%;
  &__show{
    min-height: 2.75em;
    padding: .625em;
  }
  &__text{
    flex-grow: 1;
    align-items: center;
    padding: 0;
    min-height: unset;
  }
  &__num{
    flex-grow: 1;
    text-align: right;
    margin: 0 .67em;
  }
  &__title{
    text-transform: unset;
    font-size: .875em;
    line-height: 116%;
  }
  &__icon{
    width: 1.5em;
    height: 1.5em;
  }
}

.menu__inner{
  display: block;
}

.menu-scroll{
  &__grid{
    display: grid;
    grid-template-columns: 18.75em calc(100% - 18.75em - 1.875em);
    gap: 1.875em;
  }
  &__tabs{
    padding: 1.25em .625em 0 0;
    border-right: 1px solid #EDEFF1;
  }
}

.submenu{
  display: none;
  padding: 1.25em 0 10em 0;
  &._active{
    display: block;
  }
  &._show-child{
    .submenu__inner{
      display: none;
    }
  }
  &__back{
    border-bottom: 1px solid #A7ACB0;
    display: inline-flex;
    align-items: center;
    color: #A7ACB0;
    text-transform: uppercase;
    svg{
      width: .375em;
      margin-right: .25em;
    }
    span{
      font-size: .75em;
      letter-spacing: .04em;
    }
    &:hover{
      color: $darkBlue;
      border-color: $darkBlue;
      path{
        fill: $darkBlue;
      }
    }
  }
  &__title{
    display: flex;
    align-items: center;
    min-height: 2.75em;
    img{
      width: 2em;
      margin-right: .625em;
    }
  }
}

.submenu-group{
  margin-top: 1.25em;
  &:first-child{
    margin-top: 0;
  }
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
    gap: 1.25em 1.875em;
  }
  &__tabs{
    display: flex;
    flex-wrap: wrap;
    margin-top: .625em;
  }
  &__tab{
    background: #F8F9FB;
    height: 2.5em;
    padding: 0 1.25em;
    margin: .625em .625em 0 0;
    font-weight: 500;
    color: #28292B;
    @include center;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      background: #2E8AE6;
      color: #fff;
    }
    &._active{
      background: #2E8AE6;
      color: #fff;
      pointer-events: none;
    }
    span{
      font-size: .875em;
    }
  }
}

.menu-card{
  border-radius: .25em;
  border: 1px solid #EDEFF1;
  background: #F4F7FA;
  padding: 1.25em;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: all .3s;
  color: #28292B;
  &:hover{
    border-color: $darkBlue;
    color: #1976D2;
    background-color: transparent;
  }
  &__img{
    background: #fff;
    mix-blend-mode: multiply;
    height: 6.25em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: .625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-weight: 500;
    font-size: .875em;
    line-height: 143%;
  }
  &__num{
    color: #A7ACB0;
    font-size: .75em;
    line-height: 167%;
    font-weight: 300;
  }
}

.submenu2{
  display: none;
  &._active{
    display: block;
  }
}

.menu-top__title2,
.menu-top__back{
  display: none;
}


@media screen and (max-width: 992px){
  .menu{
    &._open-level2{
      .menu-scroll__tabs{
        display: none;
      }
      .menu-top__back{
        display: flex;
      }
    }
    &._open-level3{
      .menu-top__title1{
        display: none;
      }
      .menu-top__title2{
        display: block;
      }
    }
  }

  .menu-top{
    height: 3.75em;
    display: flex;
    justify-content: unset;
    padding-left: 1.5625em;
    &__back{
      cursor: pointer;
      width: .875em;
      flex-shrink: 0;
      align-items: center;
      justify-content: center;
      margin-right: .625em;
      svg{
        width: 100%;
      }
    }
  }

  .menu-scroll{
    border-top: 1px solid #edeff1;
    height: calc(100 * var(--vh) - 3.75em);
    &__grid{
      grid-template-columns: 14.75em calc(100% - 14.75em - .625em);
      gap: .625em;
    }
    &__tabs{
      position: sticky;
      top: 0;
    }
  }

  .submenu{
    padding-right: 1.25em;
    &__back{
      display: none;
    }
  }

  .submenu-group{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: 1.25em .625em;
    }
  }
}


@media screen and (max-width: 576px) {
  .menu-scroll {
    border-top: none;
    &__grid {
      grid-template-columns: 100%;
      gap: 0;
    }
    &__tabs {
      position: static;
      padding: 0 .9375em;
      border-right: none;
    }
  }

  .submenu{
    padding: 0 .9375em 1em;
    &._active{
      display: none;
    }
    &._active-mob{
      display: block;
    }
    &__title{
      &--hide-mob{
        display: none;
        &~.submenu-group__grid{
          margin-top: 0;
        }
      }
    }
  }

  .submenu-group{
    &__tabs{
      margin-top: 0;
      @include grid100gap(.625em);
    }
    &__tab{
      margin: 0;
    }
  }

  .menu-card{
    padding: 1.25em 1em;
    &__bottom{
      display: block;
    }
  }
}