.footer{
  background-color: $darkBlack;
  &__inner{
    padding: 3.37em 0 2.94em;
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 10.0625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-weight: 500;
    text-transform: uppercase;
    color: #FFF;
    span{
      letter-spacing: .01875em;
    }
  }
  &__hidden{
    margin-top: .81em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__title,
  &__link{
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
  &__link{
    color: #D7DCE1;
    @include lastM(0 0 .83em);
  }
  &__toggle{
    display: none;
  }
  &__bottom{
    padding: 1.81em 0;
    border-top: 1px solid rgba(255, 255, 255, 0.04);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__c,
  &__link2{
    color: rgba(255, 255, 255, 0.46);
    span{
      font-size: .875em;
      line-height: 142%;
    }
  }
  &__links2{
    display: flex;
    align-items: center;
  }
  &__link2{
    margin-left: 3em;
  }
}

.footer-contacts{
  margin-top: 3.25em;
  @include grid100gap(1.25em);
  &__phone{
    color: #fff;
  }
  &__email{
    color: #fff;
    a{
      text-decoration: underline;
      color: $lightBlue;
    }
  }
  &__time{
    color: #ACB6C1;
  }
}

.socials__item,
.socials{
  display: flex;
  align-items: center;
  &__item{
    color: #B3B9BF;
    @include lastM(0 1.5em 0 0);
    img{
      width: 1.125em;
      flex-shrink: 0;
      margin-right: .44em;
    }
    span{
      font-size: 0.875em;
      line-height: 143%;
    }
  }
}

.footer-form{
  max-width: 23.3125em;
  margin-top: .81em;
  @include grid100gap(.81em);
  .text15{
    color: #B2BDCB;
  }
  &__field{
    input{
      padding: 0 1.43em;
      height: 4em;
      border-radius: .36em;
      border: 1px solid rgba(216, 221, 228, 0.09);
      background-color: transparent;
      width: 100%;
      font-size: .875em;
      color: #fff;
      &::placeholder{
        color: #72777D;
      }
      &:focus{
        border-color: #fff;
      }
    }
  }
  &__btn{
    height: 3.5em;
    span{
      font-size: 0.875em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      padding: 2.5em 0;
      flex-wrap: wrap;
    }
    ._col1{
      width: calc(40% - .75em);
      order: -1;
      margin-bottom: 2.5em;
    }
    ._col5{
      width: calc(60% - .75em);
      order: -1;
      margin-bottom: 2.5em;
    }
    &__bottom{
      flex-direction: column;
    }
    &__link2{
      margin: 0 1.5em;
    }
    &__c{
      margin-top: 2em;
      order: 2;
      text-align: center;
    }
  }

  .footer-form{
    max-width: 100%;
  }
}


@media screen and (max-width: 576px){
  .footer{
    ._col1{
      width: 100%;
    }
    ._col5{
      width: 100%;
      order: unset;
      margin-bottom: 0;
    }
    ._col2,
    ._col3,
    ._col4{
      margin-bottom: 2em;
      width: 100%;
    }
    &__title{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__toggle{
      width: .875em;
      display: flex;
      &._active{
        svg{
          transform: rotate(-180deg);
        }
      }
      svg{
        width: 100%;
        height: auto;
      }
    }
    &__hidden{
      display: none;
    }
    &__links2{
      flex-direction: column;
    }
    &__link2{
      margin: 0 0 1em;
    }
    &__c{
      margin-top: 1em;
    }
  }

  .footer-contacts{
    margin-top: 2em;
  }
}
