.header-top{
  background: #F5F6F6;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1em 10.38em 1em 0;
    position: relative;
  }
  .socials{
    &__item{
      color: #50555C;
      @include lastM(0 1em 0 0);
      &:hover{
        color: $darkBlue;
      }
      span{
        font-size: .75em;
      }
    }
  }
  &__phone{
    display: none;
  }
}

.mob-menu{
  display: flex;
  align-items: center;
  &__close{
    display: none;
  }
  &__help-btn{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 10.0625em;
    height: 2em;
    background: rgba(255, 255, 255, 0.48);
    border-radius: .25em;
    font-weight: 500;
    color: $darkBlue;
    &:hover{
      background: rgba(255, 255, 255, 0.98);
    }
    span{
      font-size: 0.9375em;
      line-height: 133%;
      text-decoration: underline;
    }
  }
  &__time{
    margin-left: 7.62em;
    display: flex;
    align-items: center;
    img{
      margin-right: .44em;
      flex-shrink: 0;
      width: .9375em;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    color: #424447;
    @include lastM(0 2.19em 0 0);
    span{
      font-size: 0.875em;
      line-height: 142%;
    }
    &:hover,
    &._active{
      color: $darkBlue;
      text-decoration: underline;
    }
  }
}

.header-bottom{
  position: relative;
  padding: .44em 0;
  background: #F8F9FB;
  &__inner{
    display: flex;
    align-items: center;
  }
}

.catalog-btn{
  width: 18.6875em;
  height: 3.5em;
  font-weight: 600;
  justify-content: space-between;
  padding: 0 1.56em;
  text-transform: uppercase;
  ._view2{
    display: none;
  }
  span{
    font-size: 0.875em;
    line-height: 143%;
  }
  &__icon{
    width: 1.25em;
    height: auto;
  }
  &__arrow{
    width: .5em;
    height: auto;
  }
}

.brand-btn,
.sale-btn{
  font-size: 1em;
  line-height: 125%;
  margin-left: 2.625em;
  color: #272D36;
  &:hover{
    color: $darkBlue;
  }
}

.action-buttons{
  margin-left: auto;
  display: flex;
}

.action-btn{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #3B434D;
  text-align: center;
  @include lastM(0 2.5em 0 0);
  &:hover{
    color: $darkBlue;
    rect,
    path{
      stroke: $darkBlue;
    }
    ._fill{
      stroke: transparent;
      fill: $darkBlue;
    }
    .action-btn__title{

    }
  }
  &__icon{
    height: 1.375em;
    margin-bottom: .3125em;
    position: relative;
    svg{
      max-height: 100%;
      width: auto;
    }
    &[data-count]{
      &:before{
        content: attr(data-count);
        position: absolute;
        font-size: .625em;
        font-weight: 500;
        line-height: 120%;
        min-width: 1.7em;
        height: 1.7em;
        padding: 0 .1em;
        top: -.5em;
        right: -1.4em;
        border-radius: 50%;
        background-color: $darkBlue;
        color: #fff;
        @include center;
      }
    }
  }
  &__title{
    font-size: .8125em;
    line-height: 154%;
  }
}


.header{
  &__logo{
    display: block;
    height: 6.81em;
    width: 12.1875em;
    padding: .81em 0 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-center{
  &__inner{
    display: flex;
    align-items: center;
    padding-right: 25.375em;
  }
}

.header-city{
  margin-left: 3.125em;
  position: relative;
  &__btn{
    display: flex;
    align-items: center;
    color: #2F3946;
  }
  span{
    font-size: .9375em;
    line-height: 133%;
  }
  &__icon{
    width: 1.125em;
    flex-shrink: 0;
    margin-right: .75em;
  }
  &__arrow{
    margin-left: .5em;
    width: .5em;
    flex-shrink: 0;
  }
}

.city-hint{
  position: absolute;
  left: -1.25em;
  top: 100%;
  margin-top: 1.25em;
  border-radius: 0.375rem;
  background: #FFF;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 8px 18px 0px rgba(0, 0, 0, 0.10), 0px 33px 33px 0px rgba(0, 0, 0, 0.09), 0px 75px 45px 0px rgba(0, 0, 0, 0.05), 0px 134px 54px 0px rgba(0, 0, 0, 0.01), 0px 209px 59px 0px rgba(0, 0, 0, 0.00);
  width: 26em;
  padding: 1.875em;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &:before{
    content: '';
    position: absolute;
    width: 1.875em;
    height: 1.875em;
    transform: translateY(-20%) rotate(45deg);
    left: 3.875em;
    top: 0;
    background: #FFF;
  }
  &__title{
    font-size: 1.1875em;
    line-height: 106%;
    font-weight: 400;
    span{
      color: $darkBlue;
    }
  }
  &__nav{
    margin-top: 1.125em;
    display: grid;
    grid-template-columns: 8.6875em calc(100% - 9.8125em);
    gap: 1.125em;
  }
  &__btn{
    height: 3.375em;
    font-weight: 400;
    padding: 0;
    border-radius: 0.3125em;
    span{
      font-size: 1em;
    }
  }
  .modal__close{
    top: .625em;
    right: .625em;
  }
}

.header-contacts{
  position: absolute;
  right: 0;
  top: 100%;
  height: 6.81em;
  display: flex;
  align-items: center;
  &__phone{
    font-size: 1.3125em;
    line-height: 95%;
    font-weight: 500;
  }
  &__desc{
    font-size: 0.8125em;
    line-height: 130%;
    color: #94989C;
    text-align: right;
  }
  &__btn{
    padding: 0 1.375em;
    height: 2.6875em;
    margin-left: 1.37em;
    font-weight: 500;
    border-radius: .25em;
  }
}

.header-search{
  margin-left: auto;
  width: 44.875em;
  position: relative;
  &__field{
    z-index: 5;
    input{
      border-radius: .36em;
      height: 4em;
      padding-left: 1.71em;
      padding-right: 12.5em;
      background-color: #fff;
      &::placeholder{
        color: #C2C6CB;
      }
    }
  }
  &__icon{
    position: absolute;
    right: 1.56em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.1875em;
    &:hover{
      path{
        stroke: $darkBlue;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__select{
    z-index: 5;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 4.69em;
    .nice-select{
      border: none;
      padding: 0 0 0 1.19em;
      border-left: 1px solid #D8DDE4;
      height: 1.375em;
      &:before{
        margin-left: .69em;
      }
      .list{
        width: auto;
        min-width: 100%;
      }
      .option{
        white-space: nowrap;
      }
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .mob-menu{
    transition: all .4s;
    transform: translateX(-120%);
    overflow-y: auto;
    position: fixed;
    display: block;
    top: 0;
    left: 0;
    background-color: #fff;
    width: 100%;
    height: $fullHeight;
    z-index: 30;
    padding: 3.44em 1.25em;
    &__help-btn{
      position: static;
      height: 2em;
      transform: unset;
      margin-top: 2.1875em;
      width: 100%;
      background: #F2F8FE;
    }
    &__time{
      margin: 1.25em 0 0;
    }
    &__close{
      display: block;
      position: absolute;
      width: .75em;
      top: 1.56em;
      right: 1.25em;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }

  .header-contacts{
    position: static;
    margin-top: 1.25em;
    height: unset;
    &__phone{
      font-size: 1.0625em;
      line-height: 117%;
    }
    &__desc{
      font-size: .625em;
      line-height: 170%;
    }
    &__btn{
      width: 9.9375em;
      height: 2.375em;
      padding: 0;
      margin-left: .625em;
      flex-grow: 1;
      span{
        font-size: .8125em;
      }
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      text-transform: uppercase;
      font-weight: 600;
      @include lastM(0 0 2.1875em);
      span{
        font-size: .75em;
        line-height: 167%;
      }
    }
  }

  .header-top{
    &__inner{
      padding: .375em 0;
    }
    .socials{
      &__item{
        img{
          margin-right: .31em;
        }
      }
    }
    &__phone{
      display: block;
      font-size: 0.9375em;
      line-height: 133%;
      font-weight: 500;
    }
  }

  .brand-btn,
  .sale-btn,
  .header-city{
    display: none;
  }

  .header{
    &__logo{
      height: 3.81em;
      padding-top: .5em;
      width: 7.0625em;
      flex-shrink: 0;
    }
    &._open-burger-menu{
      .mob-menu{
        transform: translateX(0%);
      }
    }
  }

  .burger{
    @include center;
    border-radius: 0.25em;
    background: #F0F3F5;
    width: 1.875em;
    height: 1.75em;
    flex-shrink: 0;
    margin-right: .875em;
    svg{
      width: 1em;
      height: auto;
    }
  }

  .header-search{
    position: absolute;
    right: 0;
    top: 100%;
    width: calc(100% - 10.125em);
    z-index: 3;
    &__select{
      display: none;
    }
    &__field{
      input{
        font-size: .75em;
        height: 3.83em;
        padding-left: .75em;
        padding-right: 2.25em;
      }
    }
    &__icon{
      width: .9375em;
      right: .6875em;
    }
  }

  .header-center,
  .header-bottom{
    padding: 0;
    background-color: transparent;
    &__inner{
      position: relative;
      padding: 0;
    }
  }

  .action-buttons{
    height: 3.81em;
    position: absolute;
    right: 0;
    bottom: 100%;
    align-items: center;
  }

  .action-btn{
    @include lastM(0 1.125em 0 0);
    &__title{
      display: none;
      justify-content: center;
    }
    &__icon{
      height: 1.25em;
      &[data-count]:before{
        font-size: .5625em;
        right: -.5em;
      }
    }
  }

  .catalog-btn{
    height: 2.875em;
    padding: 0 .75em;
    width: 9.5em;
    border-radius: .25em;
    span{
      font-size: .8125em;
    }
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}
