@import "../vars.scss";
.breadcrumbs {
    padding: 1.5em 0;
    &__item {
        position: relative;
        display: inline;
        color: #8E9195;
        span{
            font-size: .9375em;
            line-height: 127%;
        }
        &:last-child {
            color: #4C4E51;
        }
        &:not(:first-child) {
            margin-left: 1.5em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: .3125em;
                height: .5em;
                background-image: url(../img/triangle.svg);
                @include bgImgSize(100%);
                top: .375em;
                right: 100%;
                margin-right: .625em;
            }
        }
    }
}
