.text50{
  font-size: 3.125em;
  line-height: 116%;
}

.text35{
  font-size: 2.1875em;
  line-height: 123%;
}

.text26{
  font-size: 1.625em;
  line-height: 117%;
}

.text24{
  font-size: 1.5em;
  line-height: 117%;
}

.text22{
  font-size: 1.3125em;
  line-height: 125%;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
}

.text19{
  font-size: 1.1875em;
  line-height: 153%;
}

.text18{
  font-size: 1.125em;
  line-height: 159%;
}

.text16{
  font-size: 1em;
  line-height: 125%;
}

.text15{
  font-size: .9375em;
  line-height: 153%;
}

.text14{
  font-size: .875em;
  line-height: 121%;
}

.text13{
  font-size: .8125em;
  line-height: 121%;
}

.text12{
  font-size: .75em;
  line-height: 125%;
}

.regular-text{
  font-weight: 400;
}

.medium-text{
  font-weight: 500;
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}

.orange-text{
  color: $orange;
}

.red-text{
  color: $red;
}

.green-text{
  color: $green;
}

.dark-blue-text{
  color: $darkBlue;
}


.text{
  h3,
  h4,
  h5,
  h6,
  p,
  li{
    font-size: 1em;
    line-height: 181%;
  }
  h3,
  h4,
  h5,
  h6{
    font-weight: 700;
    @include lastM(0 0 .5em);
  }
  p{
    @include lastM(0 0 1.3em);
  }
  b{
    font-weight: 700;
  }
  ul,ol{
    @include grid100gap(.75em);
  }
  ul{
    li{
      position: relative;
      padding-left: 2.3125em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.625em;
        height: 1.625em;
        background-image: url(../img/marker.svg);
        @include bgImgSize(100%);
      }
    }
  }
  ol{
    @include grid100gap(.5em);
    counter-reset: list;
    li{
      counter-increment: list;
      &:before{
        content: counter(list) '.';
        margin-right: .5em;
        @include sameNumWidth;
      }
    }
  }
  hr{
    height: 3em;
    display: block;
    opacity: 0;
  }
  mark{
    background-color: transparent;
    padding: 0;
    color: $darkBlue;
    font-weight: 700;
  }
}

.guarantee .text ul,
.check-list{
  color: #484D53;
  @include grid100gap(.875em);
  li{
    font-size: 1em;
    line-height: 181%;
    display: flex;
    &:before{
      content: '';
      flex-shrink: 0;
      width: .9375em;
      height: .625em;
      background-image: url(../img/check3.svg);
      @include bgImgSize(100%);
      margin-right: .56em;
      margin-top: .625em;
    }
  }
}


@media screen and (max-width: 992px){
  .text50{
    font-size: 2em;
  }

  .text35{
    font-size: 1.5em;
  }

  .text26,
  .text24{
    font-size: 1.25em;
  }

  .text20,
  .text19,
  .text18{
    font-size: 1em;
  }

  .text15{
    font-size: .875em;
  }

  .text{
    font-size: .75em;
  }
}


@media screen and (max-width: 576px){
  .text50{
    font-size: 1.75em;
  }

  .text24{
    font-size: 1.125em;
  }

  .text19{
    font-size: .875em;
  }

  .check-list{
    gap: .5em;
    li{
      font-size: .875em;
      line-height: 150%;
      &:before{
        margin-top: .375em;
      }
    }
  }
}
