.info-page{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 20em) 18.8125em;
    justify-content: space-between;
  }
  &__content{
    max-width: 73.75em;
  }
  .text{
    color: #484D53;
    h3{
      font-weight: 400;
      color: $darkBlack;
      font-size: 1.25em;
      line-height: 110%;
      @include lastM(0 0 .7em);
    }
    p{
      @include lastM(0 0 .4em);
    }
  }
}

.delivery{
  margin-top: 1.19em;
  @include grid100gap(1.81em);
}

.delivery-steps{
  margin-top: 1.94em;
}

.step{
  margin-top: 1.625em;
  &__top{
    margin-bottom: 1.81em;
    padding: .94em 0;
    color: #484D53;
    border-bottom: 1px solid #EBEBEB;
    .text18{
      line-height: 161%;
    }
  }
  .text{
    ul{
      &:not(:first-child){
        margin-top: 1em;
      }
    }
  }
}

.pay-type,
.hint{
  &__icon{
    width: 4.0625em;
    height: 4.0625em;
    border-radius: 50%;
    flex-shrink: 0;
    background: #FFF;
    box-shadow: 0 .75em 1.8125em 0 rgba(0, 0, 0, 0.07);
    margin-right: .94em;
    @include center;
    img{
      width: 1.5em;
      display: block;
    }
  }
}

.pay-type{
  margin: 1.81em 0;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.65em));
  gap: 3.3em;
  &__item{
    display: flex;
  }
  &__content{
    padding-top: .88em;
  }
  &__text{
    font-size: 1em;
    line-height: 181%;
    color: #484D53;
  }
}

.hint{
  display: flex;
  padding: 1.56em;
  border-radius: .75em;
  border: 3px solid #EAF0F6;
  margin-top: 2.37em;
  .text16{
    line-height: 181%;
  }
  &__icon{
    margin-right: 1.12em;
  }
}

.delivery-way{
  margin-top: .5em;
  display: flex;
  flex-wrap: wrap;
  &__item{
    margin: 1em 1.875em 0 0;
    width: 18.75em;
    height: 5.5625em;
    @include center;
    padding: 1px 2em;
    border-radius: 0.3125em;
    border: $border;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.banners{
  @include grid100gap(1.875em);
  .sale-card{
    &__img{
      padding-bottom: 156%;
    }
  }
}

.payments{
  .text{
    margin-top: 2.82em;
    hr{
      height: 1.2em;
    }
  }
}

.pay-way{
  margin-top: 1.2em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 9em / 5));
    gap: 2.25em;
    margin-top: 1.5em;
  }
  &__item{
    display: flex;
    align-items: flex-start;
    color: $darkBlue;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .56em;
      margin-top: .4em;
    }
  }
  &__text{
    font-size: 1em;
    line-height: 137%;
    font-weight: 400;
  }
}

.guarantee{
  .text{
    margin-top: 1.19em;
    hr{
      height: 1.75em;
    }
    ul{
      gap: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .info-page{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    .banners{
      display: none;
    }
  }

  .delivery-way{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
    gap: .5em;
    &__item{
      margin: 0;
      width: 100%;
      height: 4.5em;
    }
  }

  .step{
    margin-top: 1em;
    &__top{
      margin-bottom: 1em;
      padding: .5em 0;
    }
  }

  .pay-type,
  .hint{
    &__icon{
      width: 3em;
      height: 3em;
      margin-right: .5em;
      img{
        width: 1em;
      }
    }
  }

  .pay-type{
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    &__content{
      padding-top: 0;
    }
    &__text{
      font-size: .75em;
    }
  }

  .hint{
    margin-top: 1.5em;
    padding: .75em;
    border-width: 1px;
    .text16{
      font-size: .75em;
    }
  }

  .pay-way{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 1.5em;
    }
    &__item{
      img{
        width: .75em;
        margin-top: .25em;
      }
    }
    &__text{
      font-size: .75em;
    }
  }

  .payments{
    .text{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .delivery-way{
    grid-template-columns: repeat(2,calc(50% - .25em));
    &__item{
      height: 3.5em;
    }
  }

  .pay-type{
    grid-template-columns: 100%;
  }

  .pay-way{
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
      margin-top: 1em;
    }
  }
}
