.modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullHeight;
    z-index: 100;
    text-align: center;
    overflow-y: auto;
    padding: 1em;
    opacity: 0;
    pointer-events: none;
    @media screen and (min-width: 992px){
        @media screen and (max-width: 1700px){
            font-size: 1.25em;
        }
    }
    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    &::after {
        display: inline-block;
        vertical-align: middle;
        width: 0;
        height: 100%;
        content: '';
    }
    &__bg{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: $fullHeight;
        background: rgba(60, 58, 58, 0.70);
    }
    &__dialog{
        display: inline-block;
        vertical-align: middle;
        text-align: left;
        width: 23.4375em;
        padding: 1.875em;
        max-width: 100%;
        position: relative;
        z-index: 2;
        background-color: #fff;
        box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 8px 18px 0px rgba(0, 0, 0, 0.10), 0px 33px 33px 0px rgba(0, 0, 0, 0.09), 0px 75px 45px 0px rgba(0, 0, 0, 0.05), 0px 134px 54px 0px rgba(0, 0, 0, 0.01), 0px 209px 59px 0px rgba(0, 0, 0, 0.00);
        border-radius: .375em;
    }
    &__close{
        position: absolute;
        top: 1.125em;
        right: 1.125em;
        width: .8125em;
        background-color: transparent;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                opacity: 1;
            }
        }
    }
    &__top{
        @include grid100gap(1em);
        &._center{
            text-align: center;
        }
    }
    &__title{
        font-size: 1.6875em;
        line-height: 110%;
    }
    &__desc{
        color: #606368;
    }
}

.auth-form,
.qa-info{
    margin-top: 1.125em;
    @include grid100gap(1.125em);
}

.auth-form{
    &__hint{
        color: #606368;
        &._center{
            text-align: center;
        }
        a{
            color: $darkBlue;
            text-decoration: underline;
        }
    }
    &__btn{
        height: 3.375em;
        span{
            font-size: 1em;
        }
    }
}

.auth-info{
    display: flex;
    &__wrap{
        border-radius: 0.375em;
        background: #F8F9FB;
        padding: .625em;
        color: #606368;
        a{
            color: $darkBlue;
            text-decoration: underline;
        }
    }
}

.qa-info{
    &__time{
        margin-left: 1em;
    }
    &__contacts{
        display: flex;
        justify-content: space-between;
        .text13{
            line-height: 154%;
        }
        .text22{
            line-height: 95%;
        }
    }
    &__btn{
        height: 2.6875em;
        border-radius: .25em;
    }
    &__reference,
    &__socials,
    &__text-us{
        @include grid100gap(.56em);
        width: 100%;
        text-align: center;
        color: #303335;
    }
    .socials{
        justify-content: center;
        margin-top: .13em;
        &__item{
            color: #50555C;
            @include lastM(0 1em 0 0);
        }
    }
    &__reference{
        gap: 1em;
        padding: 0 .625em;
    }
    &__links{
        border-radius: 0.375em;
        border: 1px solid #E6E9ED;
    }
    &__link{
        height: 3.125em;
        display: flex;
        align-items: center;
        padding: 0 .94em;
        color: $darkBlue;
        &:not(:last-child){
            border-bottom: 1px solid #E6E9ED;
        }
        span{
            font-size: 1em;
            line-height: 125%;
            text-decoration: underline;
            transition: inherit;
        }
        &:hover{
            span{
                text-decoration-color: transparent;
            }
        }
    }
}

.city-modal{
    .modal{
        &__dialog{
            width: 46.4375em;
        }
    }
    &__content{
        margin-top: 1.125em;
        @include grid100gap(1.125em);
    }
    &__items{
        column-count: 3;
        column-gap: 2em;
    }
    &__item{
        display: inline-block;
        width: 100%;
        font-weight: 400;
        color: $darkBlue;
        cursor: pointer;
        transition: all .3s;
        &:hover{
            color: $darkBlack;
        }
        span{
            font-size: 0.9375em;
            line-height: 267%;
        }
    }
}

.city-form{
    &__grid{
        margin-top: 1.125em;
        display: grid;
        grid-template-columns: calc(100% - 7.25em) 6.625em;
        gap: .625em;
    }
    .fg{
        input{
            height: 4em;
        }
    }
    &__btn{
        padding: 0;
        height: 3.5em;
        border-radius: 0.3125em;
    }
}

.product-modal{
    .modal{
        &__dialog{
            width: 77.5em;
        }
    }
    &__grid{
        display: grid;
        grid-template-columns: calc(100% - 34.375em) 32.5em;
        gap: 1.875em;
    }
    &__title{
        line-height: 158%;
    }
    .catalog-item__top{
        margin-top: .75em;
    }
    .props{
        margin-top: 1.625em;
    }
    .product-cart{
        box-shadow: none;
        padding: 0;
        background-color: transparent;
        margin-top: 2em;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &__nav{
            margin: 0;
            display: flex;
            gap: unset;
            .m-btn{
                height: 3.3125em;
            }
            .m-btn-blue{
                width: 13.1875em;
            }
            .m-btn-blue-outline{
                width: 15.0625em;
                margin-left: .94em;
            }
        }
        &__nav2{
            margin: 0 1em;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog{
            display: block;
        }
    }

    .product-modal{
        &__grid{
            grid-template-columns: 100%;
            gap: 1.25em;
        }
        .product-cart{
            @include grid100gap(1em);
            &__nav2{
                margin: 0;
            }
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            padding-left: 1em;
            padding-right: 1em;
        }
    }

    .auth-form{
        gap: 1em;
    }

    .qa-info{
        &__reference{
            padding: 0;
        }
    }

    .product-modal{
        .modal{
            &__dialog{
                overflow: hidden;
            }
        }

        .gallery{
            padding-top: 1em;
        }
        .product-cart{
            &__nav{
                @include grid100gap(.5em);
                .m-btn{
                    width: 100%;
                    margin: 0;
                }
            }
        }
    }
}
