.header-search{
  &._open{
    .search-result{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.search-result{
  position: absolute;
  width: 100%;
  background-color: #fff;
  top: 0;
  left: 0;
  padding: 4.375em 1.5625em 1.5625em;
  z-index: 3;
  border-radius: 0.3125em;
  border: 1px solid #DDE1E7;
  box-shadow: 0px 4px 17px 0px rgba(0, 0, 0, 0.11);
  @include grid100gap(1.25em);
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
}

.search-group{
  &__title{
    font-size: 1em;
    line-height: 137.5%;
    font-weight: 500;
  }
  &__items{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
}

.search-card{
  display: flex;
  align-items: center;
  &__img{
    width: 3.125em;
    height: 3.125em;
    margin-right: 1em;
    flex-shrink: 0;
    border-radius: .4375em;
    border: 1px solid #D8DDE4;
    img{
      border-radius: inherit;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__content{
    width: calc(100% - 4.125em);
    display: grid;
    grid-template-columns: calc(100% - 7em) 6em;
    gap: 1em;
    align-items: center;
  }
  &__text{
    max-width: 23em;
  }
  &__nums{
    text-align: right;
  }
  &__title{
    font-size: .9375em;
    line-height: 147%;
    font-weight: 500;
    color: $darkBlue;
  }
  &__count,
  &__desc{
    font-size: .8125em;
    line-height: 169%;
  }
  &__desc{
    color: $lightBlack;
  }
  &__count{
    color: #9EA1A8;
  }
  &__price{
    font-size: 0.9375em;
    font-weight: 400;
    color: #000;
  }
}


@media screen and (max-width: 992px){
  .header-search{
    &._open{
      width: 100%;
    }
  }

  .search-result{
    padding: 4.375em .94em 1.56em;
  }
}


@media screen and (max-width: 576px){
  .search-card{
    align-items: unset;
    &._type2{
      position: relative;
      padding-top: 1.7em;
      .search-card__nums{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &._type2,
    &._type3{
      .search-card__title{
        font-size: .8125em;
      }
    }
    &__content{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__title{
      font-size: .9375em;
    }
    &__nums{
      text-align: left;
    }
    &__price{
      margin-top: .4em;
      font-size: .8125em;
    }
  }
}
