.compare{
  padding-bottom: 3.125em;
  &__nav{
    display: flex;
    flex-wrap: wrap;
    margin-top: .5em;
  }
  &__btn{
    margin: .625em .625em 0 0;
    position: relative;
  }
  &__link{
    border-radius: 56.25em;
    background: #FAFAFA;
    @include center;
    padding: 0 1.3125em;
    height: 2.625em;
    span{
      font-size: 0.9375em;
      line-height: 133%;
    }
    &._active{
      pointer-events: none;
    }
    &._active,
    &:hover{
      background-color: $lightBlack;
      color: #fff;
    }
  }
  &__remove{
    position: absolute;
    width: 1.1875em;
    height: 1.1875em;
    border-radius: 50%;
    @include center;
    right: .1875em;
    top: -.3125em;
    background: #CCDCEB;
    &:hover{
      background: $darkBlue;
      path{
        stroke: #fff;
        opacity: 1;
      }
    }
    svg{
      width: .4375em;
      height: auto;
    }
  }
  &__grid{
    margin-top: 4.2em;
    position: relative;
  }
  &__content{
    grid-column: 2/-1;
  }
  .swiper-button{
    border: $border;
    &-prev{
      left: -4.5em;
    }
    &-next{
      right: -4.5em;
    }
  }
}

.compare-card,
.compare-sidebar{
  &__top{
    display: flex;
    align-items: center;
    height: 2.25em;
    margin-bottom: 1em;
  }
  &__row{
    display: flex;
    align-items: center;
    padding: .25em .94em;
    height: 2.75em;
    font-weight: 400;
    &:nth-child(odd){
      background: #FAFAFA;
    }
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
}

.compare-sidebar{
  &__rows{
    padding-top: 24.375em;
  }
}

.compare-card{
  &__top{
    justify-content: center;
  }
  &__remove{
    height: 2.25em;
    padding: 0 .875em;
    border-radius: 1.8125em;
    border: 1px solid #F6F6F6;
    color: $lightBlack;
    &:hover{
      background-color: $lightBlack;
      border-color: $lightBlack;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
    svg{
      width: 1.0625em;
      height: auto;
      margin-right: .56em;
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
  &__inner{
    border-radius: 0.3125em 0.3125em 0 0;
    border-top: $border;
    border-right: $border;
    border-left: $border;
    overflow: hidden;
    position: relative;
    height: 24.375em;
  }
  &__img{
    @include imgCover(74.8%);
  }
  .favourite-btn{
    position: absolute;
    top: 1.19em;
    right: 1.19em;
  }
  &__content{
    padding: 0 1.25em 1.5em;
  }
  &__title{
    font-size: 1.0625em;
    line-height: 135%;
    font-weight: 400;
    @include truncate(2);
  }
  &__prices{
    display: flex;
    align-items: center;
    min-height: 2.6875em;
    margin: .25em 0;
  }
  .product-card__price2{
    margin: 0 .59em;
    &:first-child{
      margin-left: 0;
    }
  }
  .product-card__sale{
    margin: 0;
  }
  .product-card__btn{
    width: 100%;
    height: 2.8125em;
  }
}

.compare-slider,
.compare-slider2{
  width: calc(100% + 1.875em);
  overflow: hidden;
  .swiper{
    &-container{
      width: 100%;
    }
    &-slide{
      width: 25%;
      padding-right: 1.875em;
    }
  }
}


.compare-header{
  transform: translateY(-120%);
  transition: all .3s;
  padding: 1.0625em 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background: #FFF;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.05), 0 .875em 1.9375em 0 rgba(0, 0, 0, 0.05), 0 3.5em 3.5em 0 rgba(0, 0, 0, 0.04), 0 7.875em 4.75em 0 rgba(0, 0, 0, 0.03), 0 14em 5.625em 0 rgba(0, 0, 0, 0.01), 0 21.875em 6.125em 0 rgba(0, 0, 0, 0.00);
  &._transform{
    transform: translateY(0);
  }
}

.compare-slider2{
  grid-column: 2/-1;
}

.compare-card2{
  display: grid;
  grid-template-columns: 5.375em calc(100% - 6em);
  gap: .625em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__prices{
    display: flex;
    align-items: center;
    min-height: 2.6875em;
    margin-bottom: .13em;
  }
  .product-card__price1{
    font-size: .8125em;
  }
  .product-card__price2{
    font-size: 1.0625em;
    margin: 0 .59em;
    &:first-child{
      margin-left: 0;
    }
  }
  .product-card__sale{
    margin: 0;
  }
  &__title{
    font-size: .9375em;
    line-height: 153%;
    font-weight: 400;
  }
}


@media screen and (max-width: 992px){
  .compare-header{
    display: none;
  }

  .compare{
    &__grid{
      grid-template-columns: 14em calc(100% - 14.5em);
      gap: .5em;
      margin-top: 1.5em;
    }
    &__content{
      grid-column: unset;
    }
    .swiper-button{
      display: none;
    }
    &__nav{
      width: calc(100% + .625em);
    }
    &__btn{
      margin-right: .25em;
    }
    &__link{
      height: 2em;
      padding: 0 1em;
      span{
        font-size: .75em;
      }
    }
    &__remove{
      right: -.25em;
    }
  }

  .compare-slider{
    width: 100%;
    overflow: unset;
    .swiper{
      &-container{
        overflow: hidden;
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: 14.5em;
        padding-right: .5em;
      }
    }
  }

  .compare-sidebar,
  .compare-card{
    &__top{
      margin-bottom: .25em;
    }
    &__row{
      padding: .125em .5em;
      height: 2em;
      span{
        font-size: .75em;
      }
    }
  }

  .compare-sidebar{
    &__rows{
      padding-top: 18em;
    }
  }

  .compare-card{
    &__inner{
      height: 18em;
    }
    &__content{
      padding: 0 .5em .75em;
    }
    &__title{
      font-size: .75em;
    }
    &__prices{
      min-height: 2em;
    }
    .product-card__price1{
      font-size: .75em;
    }
    .product-card__price2{
      font-size: .875em;
    }
    .product-card__btn{
      height: 2.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .compare{
    &__grid{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      grid-template-columns: 8em calc(100% - 8.25em);
      gap: .25em;
    }
    &__content,
    &__sidebar{
      font-size: .75em;
    }
  }

  .compare-slider{
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 11em;
        padding-right: .25em;
      }
    }
  }

  .compare-sidebar{
    &__rows{
      padding-top: 15.5em;
    }
    &__top{
      padding-left: .5em;
    }
    &__check{
      label{
        span{
          font-size: .75em;
          line-height: 120%;
        }
      }
    }
  }

  .compare-card{
    &__inner{
      height: 15.5em;
    }
    &__top{
      justify-content: unset;
    }
    &__prices{
      padding-right: 2em;
      display: block;
      position: relative;
    }
    .product-card__sale{
      position: absolute;
      top: 0;
      right: 0;
    }
    .product-card__price2{
      margin: .5em 0 0;
    }
    &__remove{
      width: 2.25em;
      span{
        display: none;
      }
      svg{
        margin: 0;
        width: .875em;
      }
    }
  }
}
