.user-data{
  padding-bottom: 2.5em;
  &__grid{
    margin-top: 2.31em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
  &__group{
    border-radius: 0.3125em;
    border: 1px solid #E9EDF2;
    padding: 1.56em;
  }
  &__title{
    font-size: 1em;
    font-weight: 600;
    text-transform: uppercase;
  }
  .user-form{
    margin-top: 1.625em;
    @media screen and (min-width: 577px){
      &__fields{
        grid-template-columns: repeat(2,calc(50% - .75em));
        gap: 1.5em;
        max-width: 100%;
      }
    }
    ._full{
      grid-column: 1/-1;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.56em;
  }
  &__save{
    width: calc(50% - .75em);
    height: 3.875em;
    span{
      font-size: 1em;
    }
  }
}

.user-subscribe,
.user-pass{
  margin-top: 2.5em;
  @include grid100gap(1.56em);
}

.user-pass{
  width: calc(50% - .75em);
}

.address{
  &__buttons{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
  &__btn{
    height: 2.5em;
    border-radius: .25em;
    svg{
      width: 1em;
      height: auto;
      margin-right: .625em;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
  .user-data__nav{
    justify-content: center;
  }
}


@media screen and (max-width: 992px){
  .user-data{
    padding-bottom: 1em;
    &__grid{
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px){
  .user-data{
    &__grid{
      margin-top: 1.5em;
    }
    &__group{
      padding: .75em;
    }
    .user-form{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__save{
      width: 100%;
    }
  }


  .user-subscribe,
  .user-pass{
    width: 100%;
    gap: 1em;
  }

  .address{
    &__buttons{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }
}
