@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-orange{
    background-color: $orange;
    color: #fff;
    &:hover{
        background-color: #FF8900;
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $darkBlack;
    border-radius: .25em;
    padding: 0 1.41em;
    height: 2.375em;
    font-weight: 700;
    text-transform: uppercase;
    &:hover{
        color: $darkBlue;
    }
    span{
        font-size: .75em;
        line-height: 167%;
        letter-spacing: .03em;
    }
}

.m-link{
    display: flex;
    align-items: center;
    font-weight: 700;
    text-transform: uppercase;
    color: $darkBlue;
    border-bottom: 1px solid $darkBlue;
    &:hover{
        border-color: transparent;
    }
    svg{
        width: .375em;
        height: auto;
        margin-left: .25em;
    }
    span{
        font-size: .75em;
        line-height: 167%;
        letter-spacing: 0.03em;
    }
}

.m-btn-blue,
.m-btn-gray{
    padding: 0 1.31em;
    height: 2.8125em;
    border-radius: 0.3125em;
    img{
        width: 1.125em;
        flex-shrink: 0;
        margin-right: .625em;
    }
    span{
        font-size: 0.9375em;
        line-height: 120%;
    }
}

.m-btn-blue{
    background-color: $lightBlue;
    color: #fff;
    &:hover{
        background-color: $darkBlue;
        color: rgba(#fff,.7);
    }
}

.m-btn-gray{
    background-color: #F4F7FA;
    color: $lightBlack;
    &:hover{
        background-color: $lightBlack;
        color: #fff;
    }
}

.m-btn-blue-outline{
    border-radius: .1875em;
    border: 1px solid $darkBlue;
    color: $darkBlue;
    &:hover{
        background-color: $darkBlue;
        color: #fff;
    }
    span{
        font-size: 0.9375em;
    }
}

.m-btn-orange-outline{
    border-radius: .1875em;
    border: 1px solid $orange;
    color: $orange;
    &:hover{
        background-color: $orange;
        color: #fff;
    }
    span{
        font-size: 0.9375em;
    }
}

.favourite-btn{
    &._active{
        path{
            fill: $darkBlue;
        }
    }
    &__icon{
        width: 1.1875em;
    }
}

.compare-btn{
    &__icon{
        width: .875em;
    }
}

.compare-btn,
.favourite-btn{
    &._active{
        color: $darkBlue;
        path{
            stroke: $darkBlue;
        }
    }
    &__icon{
        @include center;
        svg{
            width: 100%;
            height: auto;
        }
    }
}
