.category-card{
  display: block;
  text-align: center;
  color: $lightBlack;
  &:hover{
    .category-card__img{
      border-color: $darkBlue;
      border-width: 2px;
    }
    path{
      stroke: $darkBlue;
    }
  }
  &__img{
    height: 7.1875em;
    @include center;
    border: $border;
    border-radius: 0.25em;
    transition: inherit;
    margin-bottom: .625em;
    svg{
      max-width: 4.12em;
      max-height: 3.625em;
    }
  }
}

.sale-card{
  position: relative;
  color: #fff;
  &__img{
    @include imgCover(57.3%);
    img{
      border-radius: .5em;
    }
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 2.1875em;
    max-width: 15em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .badge{
    margin-bottom: .375em;
  }
  &__link{
    margin-top: .69em;
  }
}

.category-card2{
  min-height: 11.5em;
  border-radius: 0.3125em;
  background: #F2F5F8;
  position: relative;
  overflow: hidden;
  padding: 3em 0 1.625em 2.1875em;
  &:before{
    content: '';
    position: absolute;
    width: 17em;
    height: 17em;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.73);
    top: 1.125em;
    right: -7.12em;
  }
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 14em;
    height: 100%;
    position: relative;
    z-index: 2;
  }
  &__img{
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    width: 11em;
    height: 100%;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}

.brand-card{
  @include center;
  border-radius: .3125em;
  border: $border;
  height: 5.5625em;
  padding: .75em;
  img{
    max-width: 100%;
    max-height: 100%;
    display: block;
  }
}

.product-card{
  border-radius: .3125em;
  border: $border;
  transition: all .3s;
  display: flex;
  flex-direction: column;
  &:hover{
    background-color: #fff;
    box-shadow: 0px 11px 32px 0px rgba(37, 53, 69, 0.09);
    .product-card__slider{
      .swiper-pagination{
        opacity: 1;
      }
    }
    .product-card__detail{
      opacity: 1;
    }
  }
  &__top{
    position: relative;
  }
  &__badges,
  &__badges2{
    position: absolute;
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__badges{
    top: 1.25em;
    left: 1.25em;
  }
  &__badges2{
    bottom: 0;
    right: 1.25em;
  }
  &__slider{
    position: relative;
    z-index: 2;
    overflow: hidden;
    .swiper{
      &-pagination{
        opacity: 0;
        padding: 0 1.2em;
        height: calc(100% - .96em);
        bottom: .48em;
        display: flex;
        justify-content: center;
        &-bullet{
          width: 2em;
          height: 100%;
          flex-grow: 1;
          margin: 0 .06em !important;
          background-color: transparent;
          position: relative;
          &:after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            height: 0.125rem;
            width: 100%;
            background-color: $lightBlack;
            opacity: .3;
          }
          &-active{
            background-color: transparent;
            &:after{
              background-color: $darkBlue;
              opacity: 1;
            }
          }
        }
      }
    }
  }
  &__img{
    display: block;
    @include imgCover(93.3%);
  }
  .badge{
    @include lastM(0 0 .375em);
  }
  &__detail{
    opacity: 0;
    position: absolute;
    bottom: 1.56em;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 0.25em;
    background: rgba(27, 31, 35, 0.69);
    color: #fff;
    height: 1.8125em;
    display: flex;
    align-items: center;
    padding: 0 .625em;
    z-index: 4;
    white-space: nowrap;
    &:hover{
      background: rgba(27, 31, 35, .85);
    }
    img{
      width: .875em;
      flex-shrink: 0;
      margin-right: .44em;
    }
    span{
      font-size: .75em;
      line-height: 120%;
    }
  }
  &__content{
    padding: .875em 1.25em;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__title{
    font-size: 1.0625em;
    line-height: 135%;
    font-weight: 400;
    min-height: 4.05em;
    @include truncate(3);
  }
  .props{
    margin-top: 1em;
  }
  &__bottom,
  &__info{
    margin-top: 1.375em;
    justify-content: space-between;
  }
  &__feedback{
    &-count{
      margin-left: .69em;
      display: flex;
      align-items: center;
      img{
        width: 1.0625em;
        margin-right: .3125em;
        flex-shrink: 0;
      }
    }
  }
  &__status{
    font-weight: 400;
    img{
      width: .8125em;
      flex-shrink: 0;
      margin-right: .375em;
    }
  }
  &__country{
    font-weight: 400;
    img{
      width: 1.6875em;
      flex-shrink: 0;
      margin-right: .44em;
    }
  }
  &__feedback,
  &__info,
  &__status,
  &__country,
  &__bottom,
  &__stat,
  &__favourite{
    display: flex;
    align-items: center;
  }
  &__favourite{
    color: #9EA2A8;
    .favourite-btn__icon{
      width: 1.3125em;
      flex-shrink: 0;
      margin-right: .44em;
    }
  }
  &__compare{
    margin-left: 1.25em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.75em;
    min-height: 2.6875em;
  }
  &__prices{
    &-top{
      display: flex;
      align-items: center;
      margin-bottom: .5em;
    }
  }
  &__price1{
    font-size: 0.9375em;
    color: #787878;
    text-decoration: line-through;
  }
  &__price2{
    font-size: 1.375em;
    font-weight: 400;
    color: #000;
  }
  &__sale{
    @include center;
    padding: 0 .19em;
    height: 1.1875em;
    background: #1B9665;
    color: #fff;
    font-weight: 500;
    margin-left: .81em;
    border-radius: 0.25em;
    span{
      font-size: .6875em;
      line-height: 110%;
    }
  }
  &__btn{
    width: 7.5em;
    ._view2{
      display: none;
    }
    &._added{
      background-color: $darkBlue;
      color: rgba(#fff,.7);
      ._view2{
        display: block;
      }
      img,
      ._view1{
        display: none;
      }
    }
  }
}

.props{
  @include grid100gap(.25em);
  &__item{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    flex-shrink: 0;
    margin-right: .5em;
    white-space: nowrap;
    color: #606060;
  }
  &__desc{
    text-align: right;
  }
}

.collection-card{
  &__img{
    @include imgCover(57.4%);
    img{
      border-radius: .3125em;
    }
  }
  &__content{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.19em;
  }
  .text18{
    color: #73787E;
  }
  .brand-card{
    width: 10.38463em;
    height: 3.375em;
    padding: 1em 1.6em;
  }
}

.set-card,
.product-card2{
  .product-card__btn{
    width: 2.0625em;
    height: 2.0625em;
    padding: 0;
    img{
      margin: 0;
    }
    ._view2{
      width: .875em;
    }
  }
}

.product-card2{
  border-radius: .3125em;
  border: $border;
  overflow: hidden;
  display: grid;
  grid-template-columns: 7.625em calc(100% - 7.625em);
  align-items: center;
  &__img{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding: .75em .75em .75em 1.38em;
  }
  &__bottom{
    margin-top: .28em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .product-card__prices-top{
    margin-bottom: .25em;
  }
  .product-card__price1{
    font-size: .8125em;
  }
  .product-card__price2{
    font-size: .9375em;
  }
}


@media screen and (max-width: 992px){
  .sale-card{
    &__content{
      max-width: 13em;
    }
  }

  .product-card{
    .swiper{
      &-pagination{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .sale-card{
    &__content{
      padding: 1em;
      max-width: 11em;
    }
  }

  .category-card2{
    padding: 1em 0 1em 1em;
    min-height: 9em;
    &:before{
      font-size: .875em;
    }
  }

  .collection-card{
    &__content{
      flex-direction: column;
      align-items: flex-start;
    }
    .brand-card{
      margin-top: .5em;
    }
  }

  .product-card{
    &__content{
      padding: .5em .75em;
    }
    &__btn{
      width: 6.5em;
      padding: 0;
    }
  }

  .product-card2{
    grid-template-columns: 5em calc(100% - 5em);
    &__content{
      padding: .5em;
    }
    .text15{
      font-size: .75em;
    }
    .product-card__price1{
      font-size: .625em;
    }
    .product-card__price2{
      font-size: .75em;
    }
  }
}
