.cart{
  padding-bottom: 2.5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 20.625em) 18.75em;
    gap: 1.875em;
    margin-top: 1.875em;
  }
  &__groups{
    margin-top: 1.625em;
    @include grid100gap(4.3em);
  }
}

.cart-top{
  display: flex;
  align-items: center;
  padding-bottom: .94em;
  border-bottom: 1px solid #D8DDE4;
  &__num{
    width: 2.5625em;
    height: 2.5625em;
    flex-shrink: 0;
    border-radius: 50%;
    background-color: $lightBlue;
    font-weight: 600;
    margin-right: .94em;
    color: #fff;
    @include center;
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 120%;
    font-weight: 600;
  }
}

.cart-nav{
  display: flex;
  align-items: center;
  padding-left: .94em;
  margin-top: 1.5em;
  &__check{
    label{
      display: flex;
      align-items: center;
    }
  }
  &__clear{
    margin-left: 1.5em;
    border-radius: 56.25em;
    color: $darkBlue;
    font-weight: 400;
    height: 2.6875em;
    padding: 0 1.19em;
    span{
      font-size: .8125em;
    }
  }
}

.cart-card{
  padding: 1.56em .94em;
  display: grid;
  grid-template-columns: 1.3125em 7.3125em calc(100% - 50.8125em) 20em 11.875em 3.4375em;
  gap: 1.375em;
  align-items: center;
  border-bottom: 1px solid #F1F3F4;
  &__img{
    border-radius: 0.3125em;
    border: 1px solid #D8DDE4;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    max-width: 22.125em;
    @include grid100gap(.5em);
    .text16{
      line-height: 137%;
      span{
        color: $darkBlue;
      }
    }
  }
  &__calc .text14,
  &__code{
    color: #606060;
    line-height: 129%;
  }
  &__calc{
    .cartcalc{
      margin-top: .56em;
      width: 6.25em;
    }
  }
  &__price{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .product-card__price2{
      margin-top: .5em;
    }
    .product-card__sale{
      margin: .5em 0 0;
    }
  }
  &__remove{
    width: 3.4375em;
    height: 3.4375em;
    border-radius: 50%;
    padding: 0;
    &:hover{
      path{
        stroke: #fff;
      }
    }
    svg{
      width: 1.0625em;
      height: auto;
    }
  }
  &__count{
    height: 3.3125em;
    display: flex;
    align-items: center;
    margin-top: .56em;
    font-weight: 500;
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
}

.cart-card2{
  border-radius: 0.3125em;
  border: 1px solid #F1F3F4;
  overflow: hidden;
  &:not(:first-child){
    margin-top: -1px;
  }
  &__top{
    .cart-card{
      background: #FBFCFD;
      &__content{
        max-width: 32em;
        grid-column: 2/4;
        .text16{
          font-weight: 500;
        }
      }
    }
  }
  &__bottom{
    .cart-card{
      padding-left: 2.69em;
      grid-template-columns: 7.3125em calc(100% - 48.125em) 20em 11.875em 3.4375em;
      @media screen and (min-width: 577px){
        &:last-child{
          border-bottom: none;
        }
      }
      &__content{
        max-width: 23em;
      }
      .product-card__price2{
        font-size: 1.0625em;
      }
    }
  }
}

.cart-sum{
  border-radius: 0.3125em;
  border: 1px solid #E9EDF2;
  padding: 1.25em;
  position: sticky;
  top: 1em;
  &__promo-code{
    input{
      border-style: dashed;
      height: 3.64em;
    }
    label{
      font-size: .8125em;
      line-height: 169%;
      top: 1.17em;
    }
  }
  &__promo-btn{
    position: absolute;
    right: .94em;
    top: 50%;
    transform: translateY(-50%);
    color: $darkBlue;
    font-weight: 500;
    &:hover{
      color: $lightBlue;
    }
    span{
      font-size: .875em;
      line-height: 157%;
    }
  }
  &__items{
    margin-top: 1.875em;
    padding-bottom: 1.25em;
    border-bottom: 1px solid #E9EDF2;
    @include grid100gap(.375em);
    .fg,
    .m-check{
      margin: .25em 0;
    }
  }
  &__btn{
    margin-top: .69em;
    width: 100%;
    height: 2.875em;
    span{
      font-size: 1em;
    }
  }
}

.sum-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__title{
    margin-right: 1em;
  }
  .product-card__price1{
    font-size: .8125em;
  }
  &__price,
  .product-card__price2{
    text-align: right;
    font-size: 1.0625em;
  }
  &__price{
    color: #000;
    font-weight: 400;
  }
  &._last{
    height: 2.6875em;
    margin-top: .69em;
  }
}

.services{
  @include grid100gap(2.0625em);
}

.service-card{
  display: grid;
  grid-template-columns: 4.75em calc(100% - 9.875em) 2.625em;
  gap: 1.25em;
  align-items: center;
  &__img{
    border-radius: 0.3125em;
    border: 1px solid #D8DDE4;
    height: 4.75em;
    @include center;
    img{
      max-width: 2.1875em;
      max-height: 2.1875em;
      display: block;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 138%;
    font-weight: 500;
    span{
      color: $darkBlue;
    }
  }
  &__desc{
    font-size: 0.9375em;
    line-height: 147%;
    color: #616D7B;
  }
  &__items{
    padding-left: 6em;
  }
}

.service-hint{
  padding-left: 6em;
  margin-top: .28em;
  &__text{
    padding: .625em 1.19em;
    display: inline-flex;
    align-items: center;
    border-radius: 0.3125em;
    background: #F7F8FA;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 0.9375em;
      height: 0.9375em;
      background: inherit;
      left: .875em;
      top: 0;
      transform: translateY(-50%) rotate(-45deg);
    }
  }
}

.service-item{
  padding: 1.56em 0 1.56em .94em;
  display: grid;
  grid-template-columns: 1.3125em calc(100% - 13.9375em) 10em;
  gap: 1.3125em;
  align-items: flex-start;
  border-bottom: 1px solid #F1F3F4;
  &__content{
    @include grid100gap(.5em);
    max-width: 31em;
  }
  &__title{
    font-size: 0.875em;
    line-height: 157%;
    font-weight: 400;
    span{
      color: $darkBlue;
    }
  }
  &__desc{
    font-size: .8125em;
    line-height: 139%;
    color: #606060;
  }
  &__price{
    align-self: center;
    text-align: right;
    font-size: 1.375em;
    font-weight: 400;
    line-height: 120%;
  }
}

.user-form{
  margin-top: 1.25em;
  &__tabs{
    display: flex;
    align-items: center;
  }
  &__tab{
    @include lastM(0 1.625em 0 0);
  }
  &__content{
    margin-top: 1.5625em;
    display: none;
    &._active{
      display: block;
    }
  }
  &__fields{
    @include grid100gap(1.5625em);
    max-width: 30.4375em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    width: 14.6875em;
    height: 3.875em;
    span{
      font-size: 1em;
    }
  }
}

.order-form{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .845em));
  gap: 1.69em;
  &__group{
    padding: 1.56em;
    border-radius: 0.3125em;
    border: 1px solid #E9EDF2;
  }
  &__top{
    margin-bottom: .625em;
  }
  &__title{
    font-size: 0.8125em;
    line-height: 108%;
    font-weight: 600;
  }
  &__radios{
    padding-left: 1.875em;
  }
  &__radio{
    display: flex;
    align-items: center;
    min-height: 2.6875em;
  }
  &__title2{
    font-size: 1.125em;
    line-height: 239%;
    font-weight: 500;
    color: $lightBlack;
  }
  &__hint{
    width: 1.3125em;
    flex-shrink: 0;
    margin-left: .81em;
    position: relative;
    z-index: 3;
    svg{
      width: 100%;
      height: auto;
    }
  }
  .user-form__nav{
    margin-top: .5em;
    grid-column: 1/-1;
  }
}

.address-form{
  &__fields{
    max-width: 47.5625em;
    @include grid100gap(1.875em);
  }
  &__hint{
    margin-top: 1.5em;
    color: #2C2D2F;
    a{
      color: $darkBlue;
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px) {
  .cart {
    padding-bottom: 1em;
    &__grid {
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .cart-nav{
    padding-left: .75em;
    margin-top: 1em;
  }

  .cart-card{
    padding: .75em;
    grid-template-columns: 1.3125em 4em calc(100% - 25.8125em) 7.25em 7em 2.5em;
    gap: .75em;
    &__content{
      max-width: 17em;
      .text16{
        font-size: .875em;
      }
    }
    &__calc .text14,
    &__code{
      font-size: .75em;
    }
    &__remove{
      width: 2.5em;
      height: 2.5em;
      svg{
        width: 1em;
      }
    }
  }

  .cart-card2{
    &__bottom{
      .cart-card{
        grid-template-columns: 4em calc(100% - 23.75em) 7.25em 7em 2.5em;
        padding-left: 1em;
      }
    }
  }

  .order-form{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    &__group{
      padding: .75em;
    }
  }

  .address-form{
    &__fields{
      gap: 1em;
    }
    &__hint{
      margin-top: .75em;
      .text14{
        font-size: .75em;
      }
      a{
        white-space: nowrap;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .cart-top{
    padding-bottom: .5em;
    &__num{
      width: 2em;
      height: 2em;
      margin-right: .5em;
    }
    &__num span,
    &__title{
      font-size: .75em;
    }
  }

  .cart-nav{
    padding-left: .25em;
  }

  .cart-items{
    @include grid100gap(.75em);
  }

  .cart-card{
    border: 1px solid #f1f3f4;
    border-radius: .3125em;
    padding: .5em .5em .5em 2em;
    position: relative;
    grid-template-columns: 7em calc(100% - 7.75em);
    row-gap: .75em;
    &__check{
      position: absolute;
      left: .25em;
      top: .5em;
    }
    &__img{
      grid-column: 1/-1;
      max-width: 5em;
    }
    &__content{
      grid-column: 1/-1;
      gap: .25em;
      .text16{
        font-size: .75em;
      }
    }
    &__calc .text14,
    &__code{
      font-size: .625em;
    }
    &__action{
      position: absolute;
      right: .25em;
      top: .5em;
    }
    &__count{
      height: unset;
    }
    .cartcalc{
      height: 2.5em;
    }
    .product-card__price1{
      font-size: .75em;
    }
    .product-card__price2{
      font-size: .875em;
      margin-top: .25em;
    }
    .product-card__sale{
      margin-top: .25em;
    }
  }

  .cart-card2{
    &__top{
      .cart-card{
        &__content{
          padding-right: 3em;
          grid-column: 1/-1;
        }
      }
    }
    &__bottom{
      padding: .5em;
      @include grid100gap(.5em);
      .cart-card{
        padding: .5em .5em .5em 2em;
        grid-template-columns: 7em calc(100% - 7.75em);
        &__content{
          grid-column: 1/-1;
        }
        .product-card__price2{
          font-size: .8125em;
        }
      }
    }
  }

  .cart-sum{
    padding: .75em;
  }

  .services{
    gap: 1em;
  }

  .service-card{
    grid-template-columns: 3.375em calc(100% - 7em) 2.625em;
    gap: .5em;
    align-items: flex-start;
    &__img{
      height: 3.375em;
    }
    &__title{
      font-size: .875em;
    }
    &__desc{
      font-size: .75em;
    }
    &__items{
      padding-left: 2em;
    }
  }

  .service-hint{
    margin-top: .5em;
    padding-left: 0;
  }

  .service-item{
    grid-template-columns: 1.3125em calc(100% - 1.8125em);
    gap: .5em;
    padding: 1em 0;
    &__price{
      grid-column: 2/3;
      font-size: 1.25em;
    }
    &__content{
      gap: .25em;
    }
    &__title{
      font-size: .75em;
    }
    &__desc{
      font-size: .625em;
    }
  }

  .user-form{
    margin: 0;
    &__tabs{
      display: block;
    }
    &__tab{
      @include lastM(0 0 1em);
    }
    &__fields{
      gap: 1em;
    }
    &__btn{
      width: 100%;
    }
  }

  .order-form{
    grid-template-columns: 100%;
    gap: .5em;
  }
}
