.catalog-item{
  margin-top: 1.625em;
  &__inner{
    display: grid;
    grid-template-columns: 39.375em calc(100% - 41.25em);
    gap: 1.875em;
  }
  &__wrap{
    display: grid;
    grid-template-columns: 32.5em calc(100% - 34.375em);
    gap: 1.875em;
    margin-top: .75em;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  .props{
    margin-top: 1.56em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2em;
  }
  &__link{
    height: 2.25em;
    padding: 0 1em;
    @include lastM(0 0 .625em);
    svg{
      margin-left: .375em;
      width: .3125em;
      height: auto;
    }
    span{
      border: none;
    }
    &:hover{
      path{
        fill: #fff;
      }
    }
  }
}

.gallery{
  &__img{
    display: block;
    @include imgCover(93.6%);
    img{
      border-radius: 0.3125em;
      border: $border;
      transition: all .3s;
    }
  }
  &__small{
    margin-top: .94em;
    width: calc(100% + .94em);
    .swiper{
      &-slide{
        width: 25%;
        padding-right: .94em;
        &:hover,
        &-thumb-active{
          img{
            border-color: $lightBlue;
          }
        }
      }
    }
  }
  &__big{
    position: relative;
  }
  .swiper{
    &-nav{
      display: flex;
      align-items: center;
      bottom: 3em;
      right: 1.875em;
      position: absolute;
    }
    &-button{
      position: static;
      width: .625em;
      height: auto;
      margin: 0 0 0 1.5em;
    }
  }
}

.zoom{
  position: absolute;
  left: 1.875em;
  bottom: 1.875em;
  width: 3.25em;
  height: 3.25em;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0px 5px 12px 0px rgba(0, 0, 0, 0.07);
  pointer-events: none;
  z-index: 2;
  @include center;
  img{
    width: 1em;
    height: auto;
  }
}

.brand-info{
  display: grid;
  grid-template-columns: 13.625em calc(100% - 14.875em);
  gap: 1.25em;
  align-items: center;
  .brand-card{
    height: 5.5625em;
    padding: 1.5em 2.25em;
  }
  &__items{
    @include grid100gap(.44em);
  }
  &__item{
    font-size: .8125em;
    line-height: 123%;
    color: #51555B;
    a{
      text-decoration: underline;
      color: $darkBlue;
    }
    span{
      color: #A1A6AC;
    }
  }
}

.product-review{
  margin-left: 1em;
  flex-shrink: 0;
  padding-top: .81em;
  .product-card__feedback-count{
    margin: .31em 0 0;
  }
}

.props{
  &__items{
    @include grid100gap(.375em);
  }
  &__nav{
    margin-top: .375em;
    @include center;
  }
  &__link{
    display: flex;
    align-items: center;
    color: $darkBlue;
    &:hover{
      span{
        text-decoration-color: transparent;
      }
    }
    svg{
      width: .625em;
      height: auto;
      margin-left: .625em;
    }
    span{
      transition: all .3s;
      font-size: .9375em;
      line-height: 306%;
      text-decoration-line: underline;
    }
  }
}

.prop-item{
  display: flex;
  justify-content: space-between;
  padding: .19em .94em;
  &:nth-child(even){
    background: #FAFAFA;
  }
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 133%;
    font-weight: 400;
  }
  &__desc{
    text-align: right;
    color: #5E636A;
  }
  &__title{
    display: flex;
    align-items: center;
  }
  &__hint{
    width: 1.1875em;
    flex-shrink: 0;
    margin-left: .625em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.product-cart{
  position: sticky;
  top: 1em;
  border-radius: .3125em;
  background: #FFF;
  box-shadow: 0 .25em 1.875em 0 rgba(0, 0, 0, 0.07);
  padding: 1.75em 2.25em 2.75em;
  &__price{
    display: flex;
    align-items: center;
    .product-card__price2{
      font-size: 2.0625em;
    }
    .product-card__prices-top{
      margin: 0 0 0 .81em;
    }
    .product-card__price1{
      font-size: 1.25em;
    }
    .product-card__sale{
      padding: 0 .5em;
      height: 1.5em;
      margin-left: .81em;
      span{
        font-size: .875em;
      }
    }
  }
  .product-card__status{
    margin: .625em 0 0;
  }
  &__nav{
    display: grid;
    grid-template-columns: 6.25em calc(100% - 7.45em);
    gap: 1.2em;
    margin-top: 1.625em;
  }
  .product-card__btn{
    height: 3.3125em;
    width: 100%;
    span{
      font-size: 1.0625em;
    }
  }
  &__quick-buy{
    margin-top: 1.2em;
    height: 2.5em;
    width: 100%;
    svg{
      width: 1.25em;
      height: auto;
      margin-right: .625em;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
  &__nav2{
    display: flex;
    align-items: center;
    margin-top: 1.2em;
  }
  &__btn2{
    font-weight: 300;
    display: flex;
    align-items: center;
    color: #676C73;
    @include lastM(0 1.5em 0 0);
    .compare-btn__icon,
    .favourite-btn__icon{
      margin-right: .44em;
    }
    span{
      font-size: 0.875em;
      line-height: 120%;
    }
  }
  &__items{
    margin-top: 1.2em;
  }
}

.product-spoiler{
  border-bottom: 1px solid rgba(66, 79, 96, 0.09);
  &__show{
    padding: 1.125em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 400;
  }
  &__btn{
    width: .5em;
    flex-shrink: 0;
    margin-left: .5em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.catalog-detail{
  padding: 4.25em 0 2.5em;
  &__tabs{
    display: flex;
    border-bottom: $border;
  }
  &__tab{
    display: flex;
    align-items: center;
    height: 3.375em;
    font-weight: 500;
    text-transform: uppercase;
    border-bottom: 1px solid transparent;
    transition: all .3s;
    cursor: pointer;
    @include lastM(0 5em 0 0);
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: $darkBlue;
      border-color: $darkBlue;
    }
  }
  .tab-content{
    margin-top: 3.125em;
  }
}

.set{
  @include grid100gap(3.125em);
}

.set-group{
  &__grid{
    margin-top: 1.25em;
  }
}

.set-card{
  padding: .75em .5em;
  border-radius: .3125em;
  border: $border;
  display: grid;
  grid-template-columns: 6.8125em calc(100% - 7.875em);
  gap: 1.0625em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: .9375em;
    line-height: 153%;
    font-weight: 400;
    min-height: 4.59em;
    display: flex;
    align-items: center;
  }
  .product-card__status{
    margin-top: .69em;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__price{
    font-size: 1.125em;
    font-weight: 400;
  }
}

.product-props{
  &__text,
  &__items{
    column-count: 2;
    column-gap: 8.75em;
  }
  &__text{
    margin-top: 1.81em;
    color: #484D53;
    p{
      @include lastM(0 0 .75em);
    }
  }
  &__items{
    margin-top: 2.19em;
  }
  &__advantages{
    margin-top: 3em;
    max-width: 57em;
  }
}

.props-group{
  display: inline-block;
  width: 100%;
  @include lastM(0 0 2.625em);
  &__title{
    font-size: 1.0625em;
    line-height: 118%;
    font-weight: 600;
    color: $lightBlack;
  }
  .props__items{
    margin-top: 1.31em;
  }
}

.reviews{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 20.625em) 18.75em;
    gap: 1.875em;
    margin-top: 2em;
  }
  &__items{
    @include grid100gap(1.875em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
  }
  &__link{
    width: 20.625em;
    height: 2.5em;
    border-radius: .25em;
    font-weight: 400;
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
}

.review{
  padding-bottom: 1.25em;
  border-bottom: $border;
  &__name{
    font-size: 1.125em;
    line-height: 139%;
    font-weight: 600;
  }
  &__rating{
    display: flex;
    align-items: center;
    .rating{
      margin-right: .56em;
    }
  }
  &__date{
    font-size: 0.75em;
    line-height: 208%;
    color: $lightBlack;
  }
  &__items{
    margin-top: 1.13em;
    @include grid100gap(.5em);
  }
  &__item{
    font-size: .875em;
    line-height: 164%;
    b{
      font-weight: 800;
    }
  }
  &__images{
    display: flex;
    flex-wrap: wrap;
  }
  &__img{
    margin: .5em .75em 0 0;
    width: 12em;
    &-inner{
      display: block;
      @include imgCover(61.4%);
    }
    img{
      border-radius: .25em;
    }
  }
}

.review-answer{
  width: calc(100% - 2.875em);
  margin: 1.5em 0 0 auto;
  padding: 0 .94em .94em;
  border-radius: 0.3125em;
  border: 1px solid #5EB9FC;
  @include grid100gap(.5em);
  &__top{
    display: flex;
    align-items: center;
    padding: 1.06em 0;
    font-weight: 600;
    color: $darkBlue;
    border-bottom: 1px solid #5EB9FC;
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .625em;
    }
    span{
      font-size: 1.125em;
      line-height: 138%;
    }
  }
  &__text{
    font-size: 0.875em;
    line-height: 164%;
    color: #363636;
    letter-spacing: .00875em;
    b{
      font-weight: 400;
    }
    & * {
      line-height: inherit;
    }
  }
  &__date{
    font-size: .75em;
    line-height: 208%;
    letter-spacing: .0075em;
    color: $lightBlack;
  }
}

.reviews-stat{
  position: sticky;
  top: 1em;
  padding: 1.56em 1.875em;
  border-radius: .3125em;
  border: $border;
  &__title{
    font-weight: 400;
    font-size: 1em;
    line-height: 125%;
    letter-spacing: .01em;
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
  }
  &__num{
    font-size: 3em;
    font-weight: 400;
  }
  &__rating{
    margin-left: .69em;
    @include grid100gap(.25em);
    .text12{
      font-weight: 400;
      color: $lightBlack;
    }
  }
  &__items{
    margin-top: 1.94em;
    @include grid100gap(.875em);
  }
}

.review-item{
  display: grid;
  grid-template-columns: 1.8125em calc(100% - 5.125em) 1.3125em;
  gap: 1em;
  align-items: center;
  &__count{
    font-size: .75em;
    color: #9E9E9E;
    font-weight: 400;
  }
  &__num{
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include sameNumWidth;
    color: #565656;
    font-weight: 400;
    span{
      font-size: .75em;
      letter-spacing: .015em;
    }
    img{
      width: .8125em;
      flex-shrink: 0;
    }
  }
  &__progressbar{
    border-radius: 5.625rem;
    background: #ECEFF3;
    width: 100%;
    height: .25em;
    position: relative;
    span{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      border-radius: inherit;
      background-color: $darkBlue;
    }
  }
}

.review-form{
  margin-top: 6.87em;
  &__wrap{
    margin-top: 2.125em;
    max-width: 45.3125em
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
  ._full{
    grid-column: 1/-1;
  }
  &__nav{
    margin-top: 1.56em;
    @include center;
  }
  &__btn{
    width: 12.5em;
    height: 4.125em;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    margin-top: 1em;
    padding-bottom: 2em;
    &__inner{
      grid-template-columns: 20em calc(100% - 21.25em);
      gap: 1.25em;
      margin-top: 1em;
    }
    &__wrap{
      grid-template-columns: 100%;
    }
    .gallery{
      position: sticky;
      top: 1em;
    }
    &__top{
      display: block;
    }
    &__title{
      display: none;
    }
  }

  .brand-info{
    grid-template-columns: 7.5em calc(100% - 8em);
    gap: .5em;
    .brand-card{
      padding: .75em 1.25em;
      height: 4em;
    }
  }

  .product-review{
    margin-left: 0;
  }

  .product-cart{
    padding: 1em 1.25em;
  }

  .catalog-detail{
    padding: 0 0 1em;
    &__tabs{
      @include fullScroll;
    }
    &__tab{
      height: 2.25em;
      @include lastM(0 1.5em 0 0);
    }
    .tab-content{
      margin-top: 1.75em;
    }
  }

  .product-props{
    &__items,
    &__text{
      margin-top: 1.5em;
      column-count: 1;
    }
  }

  .reviews{
    &__grid{
      grid-template-columns: calc(100% - 16em) 15em;
      gap: 1em;
      margin-top: 1.25em;
    }
    &__items{
      gap: 1.5em;
    }
  }

  .review{
    padding-bottom: 1em;
    &__images{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
      margin-top: 1em;
    }
    &__img{
      width: 100%;
      margin: 0 !important;
    }
    &__items{
      margin-top: .875em;
      gap: .25em;
    }
    &__item{
      font-size: .75em;
    }
  }

  .reviews-stat{
    padding: 1em;
    &__title{
      font-size: .875em;
    }
    &__info{
      margin-top: .75em;
    }
    &__items{
      margin-top: 1em;
    }
  }

  .review-answer{
    width: calc(100% - 1em);
    margin-top: 1em;
    padding: 0 .5em .5em;
    gap: .25em;
    &__top{
      padding: .5em 0;
      span{
        font-size: 1em;
      }
    }
    &__text{
      font-size: .75em;
    }
  }

  .review-form{
    margin-top: 3em;
    &__wrap{
      margin-top: 1.25em;
    }
    &__fields{
      grid-template-columns: 100%;
      gap: 1em;
    }
    .fg__hint{
      position: static;
      margin: .5em 0 0;
      transform: unset;
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
    }
    &__title{
      font-size: 1.25em;
    }
    &__links{
      margin-top: 1.5em;
    }
    &__link{
      height: 2em;
      span{
        font-size: .75em;
      }
    }
  }

  .gallery{
    .swiper-nav,
    &__small{
      display: none;
    }
    .zoom{
      display: none;
    }
    &__big{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 95%;
        padding-right: .5em;
      }
    }
  }

  .prop-item{
    padding-left: .5em;
    padding-right: .5em;
    &__title,
    &__desc{
      font-size: .75em;
    }
  }

  .product-cart{
    &__price{
      flex-direction: column-reverse;
      align-items: flex-start;
    }
    .product-card__prices-top{
      margin: 0 0 .75em;
    }
    &__nav{
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__quick-buy,
    .product-card__btn,
    .cartcalc{
      height: 2.75em;
    }
    &__quick-buy{
      margin-top: .5em;
    }
    .product-card__btn{
      span{
        font-size: .9375em;
      }
    }
    &__nav2{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn2{
      @include lastM(0 0 1em);
    }
  }

  .product-spoiler{
    &__show{
      padding: .75em 0;
    }
    &__title{
      font-size: .875em;
    }
  }

  .set-card{
    padding: .25em;
    grid-template-columns: 4em calc(100% - 4.5em);
    gap: .5em;
    &__title{
      font-size: .75em;
    }
    .product-card__status{
      span{
        font-size: .75em;
      }
    }
    &__bottom{
      margin-top: .69em;
    }
  }

  .reviews{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
