.catalog{
  &__inner{
    display: grid;
    grid-template-columns: 18.75em calc(100% - 20.625em);
    gap: 1.875em;
  }
  &__search{
    position: relative;
    &-btn{
      position: absolute;
      right: 1.25em;
      top: 50%;
      transform: translateY(-50%);
      width: 1.125em;
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        path{
          stroke: $darkBlue;
        }
      }
    }
    input{
      height: 3.64em;
      padding: 0 2.8em 0 1.43em;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    &-left{
      display: flex;
    }
    .brand-card{
      width: 7.625em;
      height: 3.2em;
      margin-left: 1.75em;
    }
  }
  &__count{
    color: #626465;
    align-self: flex-end;
    margin-left: .56em;
    padding-bottom: .375em;
  }
  &__items{
    margin-top: 2.19em;
  }
}

.filter{
  border-radius: 0.3125em;
  border: 1px solid #E9EDF2;
  padding: 1.56em;
  margin-top: 1.69em;
  &__bg{
    display: none;
  }
  &__groups{
    @include grid100gap(1.875em);
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__btn{
    height: 2.875em;
    padding: 0 1.06em;
    flex-grow: 1;
    @include lastM(0 .94em 0 0);
    span{
      font-size: 1em;
    }
  }
}

.filter-group{
  &._active{
    .filter-group__toggle{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    text-transform: uppercase;
    font-weight: 400;
    color: #000;
  }
  &__toggle{
    width: .5em;
    position: relative;
    &:hover{
      path{
        fill: $darkBlue;
      }
    }
    &:before{
      content: '';
      @include centerAbsolute;
      width: 300%;
      height: 300%;
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__bottom{
    padding-top: .81em;
    display: none;
    .catalog__search{
      margin-bottom: .81em;
    }
  }
  &__checkboxes{
    width: calc(100% + 1em);
    overflow-y: auto;
    max-height: 24em;
    padding: .625em 1em .625em 0;
    @include grid100gap(.75em);
    @include scroll;
  }
  &__check{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-count{
      border-radius: 5.625em;
      border: 1px solid #F2F4F7;
      padding: 0 .81em;
      display: flex;
      align-items: center;
      height: 1.5em;
      span{
        font-size: .6875em;
        line-height: 120%;
      }
    }
  }
}

.check-group{
  @include grid100gap(.75em);
  &__items{
    @include grid100gap(.75em);
  }
}

.filter-slider{
  padding-top: .7em;
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #E4E4E4;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background-color: #2E6BC6;
    }
    .noUi-handle{
      border: none;
      box-shadow: none;
      background-color: #2E6BC6;
      width: .25em;
      height: 1em;
      top: -.4375em !important;
      right: -.0625em !important;
      border-radius: 0;
      &:before{
        opacity: 0;
        width: 400%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .4375em));
    gap: .875em;
  }
  .fg{
    input{
      height: 2.85em;
      padding-left: 3.14em;
      font-weight: 300;
      color: #515356;
    }
    span{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-50%);
      font-size: .875em;
      line-height: 120%;
      color: #B2B5B9;
    }
  }
}

.popular-collection{
  border-radius: 0.3125em;
  border: 1px solid #E9EDF2;
  padding: 1.56em;
  margin-top: .44em;
  &__links{
    margin-top: 1.56em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    text-decoration-line: underline;
    color: $darkBlue;
    @include lastM(0 0 1.25em);
    span{
      font-size: .9375em;
      line-height: 127%;
    }
  }
}

.catalog-collection{
  margin-top: 2em;
  padding-bottom: 2em;
  .product-slider{
    &>.swiper-container>.swiper-wrapper>.swiper-slide{
      width: 100%;
      &-active~.swiper-slide{
        opacity: 0 !important;
        pointer-events: none !important;
      }
    }
    &>.swiper-button{
      width: unset;
      height: unset;
      border: none;
      margin: 0;
      top: 100%;
      &:hover{
        background-color: transparent;
      }
      &-prev{
        left: 50%;
        transform: translate(calc(-1.5em), 120%);
      }
      &-next{
        right: 50%;
        transform: translate(calc(1.5em), 120%);
      }
    }
  }
  .product-card{
    border-color: $darkBlue;
  }
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: 1.25em;
    .badge{
      flex-shrink: 0;
      height: 1.625em;
      padding: 0 .5em;
      margin-right: .625em;
    }
  }
}

.mob-filter{
  &__btn{
    display: none;
  }
}

.type-filter{
  margin-top: 1em;
  display: flex;
  flex-wrap: wrap;
  .filter-item{
    margin: .94em .94em 0 0;
  }
  &__wrap{
    margin-top: 1.94em;
    .type-filter{
      margin-top: .06em;
    }
  }
}

.filter-item{
  input:checked+.filter-item__inner{
    color: $darkBlue;
    border-color: $darkBlue;
    .filter-group__check-count{
      background-color: $darkBlue;
      color: #fff;
      border-color: $darkBlue;
    }
  }
  &__inner{
    padding: 0 1.25em;
    border-radius: .3125em;
    border: $border;
    height: 3.25em;
    display: flex;
    align-items: center;
  }
  .filter-group__check-count{
    margin-left: .625em;
    flex-shrink: 0;
  }
}

.sort-filter{
  display: flex;
  align-items: center;
  margin-top: 1.5em;
  &__items{
    margin-left: 1.0625em;
    display: flex;
    align-items: center;
  }
}

.sort-item{
  @include lastM(0 2.12em 0 0);
  input:checked+.sort-item__text{
    color: $darkBlack;
    border-color: $darkBlue;
  }
  &__text{
    color: #74777C;
    height: 2.1875em;
    display: flex;
    align-items: center;
    border-bottom: 1px solid transparent;
  }
}

.pagination{
  margin-top: 3.75em;
  &__more{
    height: 3.125em;
    width: 100%;
    span{
      border-top: 1px solid transparent;
      border-bottom: 1px dotted $darkBlue;
    }
  }
  &__items{
    display: flex;
    justify-content: center;
    margin-top: 2.5em;
  }
  &__item{
    height: 3.625em;
    padding: 0 1.5em;
    border-radius: .3125em;
    border: 1px solid #E9EAEE;
    @include center;
    margin: 0 .3125em;
    font-weight: 400;
    svg{
      width: 1.1875em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $lightBlue;
      border-color: $lightBlue;
      path{
        fill: #fff;
      }
    }
  }
}

.brands2{
  margin-top: 5em;
  &__grid{
    margin-top: 1.875em;
  }
}

.info2{
  margin-top: 3.75em;
  .text{
    margin-top: 1.56em;
    color: #484D53;
    p{
      @include lastM(0 0 .5em);
    }
    h3{
      font-size: 1.1875em;
      line-height: 143%;
    }
    hr{
      height: .5em;
      display: block;
      opacity: 0;
    }
  }
}

.selected-filter{
  display: flex;
  flex-wrap: wrap;
  margin-top: .625em;
  &__item{
    flex-shrink: 0;
    margin: .625em .625em 0 0;
    border-radius: 56.25rem;
    background: #FAFAFA;
    min-height: 2.625em;
    display: flex;
    align-items: center;
    padding: .25em 1.31em;
    &._blue{
      color: $darkBlue;
      font-weight: 400;
      background: #F2F5F8;
    }
    &-remove{
      width: .625em;
      flex-shrink: 0;
      margin-left: .625em;
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        path{
          opacity: 1;
        }
      }
    }
  }
  &__clear{
    flex-shrink: 0;
    margin-top: .625em;
    padding: 0 .94em;
    height: 2.625em;
    border-radius: 56.25em;
  }
}


@media screen and (max-width: 992px){
  .popular-collection,
  .catalog-collection{
    display: none;
  }

  .catalog{
    &__inner{
      grid-template-columns: 100%;
    }
    &__items{
      margin-top: 1.5em;
    }
  }

  .mob-filter{
    display: grid;
    grid-template-columns: calc(100% - 3.6875em) 3.1875em;
    gap: .5em;
    &__btn{
      display: flex;
      width: 3.1875em;
      height: 3.1875em;
      svg{
        width: 1.25em;
        height: auto;
      }
    }
  }

  .filter{
    padding: 1em;
    border-radius: 0;
    height: $fullHeight;
    overflow-y: auto;
    width: 15.5em;
    top: 0;
    left: 0;
    position: fixed;
    z-index: 20;
    background-color: #fff;
    border: none;
    margin: 0;
    transform: translateX(-120%);
    transition: all .3s;
    &._active{
      transform: translateX(0%);
      &~.filter__bg{
        display: block;
      }
    }
    &__bg{
      position: fixed;
      z-index: 19;
      top: 0;
      left: 0;
      width: 100%;
      height: $fullHeight;
      background: rgba(15, 20, 27, .6);
    }
    &__nav{
      flex-wrap: wrap;
    }
    &__btn{
      width: 100%;
      @include lastM(0 0 .75em);
    }
  }

  .filter-group{
    &__checkboxes{
      overflow: unset;
      max-height: unset;
      width: 100%;
      padding-right: 0;
    }
  }

  .type-filter{
    flex-wrap: unset;
    @include fullScroll;
    .filter-item{
      flex-shrink: 0;
      margin-top: 0;
    }
  }

  .sort-filter{
    display: block;
    &__items{
      margin: 0;
      flex-wrap: wrap;
    }
  }

  .sort-item{
    margin: .75em 1.25em 0 0 !important;
  }

  .pagination{
    margin-top: 2em;
    &__more{
      height: 2.5em;
    }
    &__items{
      margin-top: 1em;
    }
    &__item{
      margin: 0 .125em;
      height: 2.5em;
      padding: 0 .475em;
      &:first-child{
        margin-left: 0;
      }
      &:last-child{
        margin-right: 0;
      }
      svg{
        width: .875em;
      }
    }
  }

  .brands2{
    margin-top: 3em;
    &__grid{
      margin-top: 1.25em;
    }
  }

  .info2{
    margin-top: 3em;
  }

  .selected-filter{
    flex-wrap: unset;
    @include fullScroll;
    &__item{
      padding-left: .94em;
      padding-right: .94em;
      span{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__top{
      flex-direction: column;
      align-items: flex-start;
      &-left{
        display: block;
      }
      .text35{
        span{
          white-space: nowrap;
        }
      }
      .brand-card{
        margin: 1em 0 0;
      }
    }
    &__count{
      margin: .5em 0 0;
      padding: 0;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: 1em .5em;
      .product-card{
        &__title{
          font-size: .875em;
        }
        &__badges{
          top: .5em;
          left: .5em;
        }
        &__badges2{
          display: none;
        }
        &__content{
          padding: .5em;
        }
        .props{
          display: none;
        }
        &__info{
          display: block;
          margin-top: .75em;
          &._type2{
            display: flex;
          }
        }
        &__status{
          margin-top: .75em;
        }
        &__bottom{
          display: block;
          padding-top: 1em;
        }
        &__btn{
          width: 100%;
          margin-top: .5em;
        }
        &__country{
          .text14{
            display: none;
          }
        }
        .compare-btn{
          margin-left: 1em;
          &__icon{
            width: .75em;
          }
        }
        .favourite-btn{
          &__icon{
            width: 1em;
          }
        }
        &__price1{
          font-size: .75em;
        }
        &__price2{
          font-size: 1.125em;
        }
      }
    }
  }

  .brands2{
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
    .brand-card{
      height: 3.5em;
    }
  }
}
