@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.section{
  padding: 2.5em 0;
  &--first{
    padding-top: 0;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.875em;
  }
}

.swiper-button{
  width: 3.125em;
  height: 3.125em;
  border-radius: 50%;
  order: $border;
  svg{
    width: .625em;
    height: auto;
  }
  &._white{
    background-color: #fff;
  }
  &:hover{
    background-color: #F0F2F6;
    path{
      fill: $darkBlue;
    }
  }
}

.swiper-pagination{
  display: flex;
  align-items: center;
  &-bullet{
    background-color: #fff;
    opacity: .7;
    width: 0.375em;
    height: 0.375em;
    &:not(:last-child){
      margin: 0 1.875em 0 0 !important;
    }
    &:last-child{
      margin: 0 !important;
    }
    &-active{
      opacity: 1;
      width: 0.625em;
      height: 0.625em;
    }
  }
}

.badge{
  @include center;
  padding: 0 .625em;
  text-transform: uppercase;
  font-weight: 500;
  border-radius: 0.1875em;
  background: #F3467A;
  height: 1.5em;
  color: #fff;
  span{
    font-size: .6875em;
  }
  &._green{
    background: #55CD62;
  }
  &._orange{
    background: $orange;
  }
  &._dark{
    font-weight: 300;
    text-transform: unset;
    background: rgba(27, 31, 35, 0.69);
  }
}

.grid-column3,
.grid-column4,
.grid-column5{
  display: grid;
  gap: 1.875em;
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
}

.grid-column5{
  grid-template-columns: repeat(5,calc(20% - 7.5em / 5));
}

.grid-column2{
  display: grid;
  gap: 2.5em;
  grid-template-columns: repeat(2,calc(50% - 1.25em));
}

.tab-slider{
  position: relative;
  overflow: hidden;
  .swiper{
    &-container{
      position: relative;
      &:before,
      &:after{
        content: '';
        position: absolute;
        width: 11.125em;
        height: 4.625em;
        top: 50%;
        transform: translateY(-50%);
        z-index: 2;
      }
      &:before{
        left: -7.1em;
        background: linear-gradient(270deg, #FFF 65.18%, rgba(255, 255, 255, 0.00) 100%);
        transform: translateY(-50%) rotate(-180deg);
      }
      &:after{
        right: -7.1em;
        background: linear-gradient(270deg, #FFF 65.18%, rgba(255, 255, 255, 0.00) 100%);
      }
    }
    &-slide{
      width: auto;
      padding-right: .625em;
    }
    &-button{
      z-index: 3;
      width: .625em;
      height: auto;
      transform: translateY(-50%);
      margin: 0;
      &-prev{
        left: 0;
        &.swiper-button-disabled~.swiper-container:before{
          opacity: 0;
        }
      }
      &-next{
        right: 0;
        &.swiper-button-disabled~.swiper-container:after{
          opacity: 0;
        }
      }
      &.swiper-button-disabled{
        opacity: 0;
      }
    }
  }
}

.tab{
  padding: 0 1.2em;
  border-radius: 56.25em;
  border: 1px solid #F2F4F5;
  display: flex;
  align-items: center;
  height: 2.6875em;
  color: $lightBlack;
  font-weight: 400;
  cursor: pointer;
  transition: all .3s;
  span{
    font-size: .875em;
  }
  &._active,
  &:hover{
    background: #F2F5F8;
    border-color: #F2F5F8;
    color: $darkBlue;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.col5-slider{
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      width: 20%;
      padding-right: 1.875em;
    }
  }
}


@media screen and (max-width: 992px){
  .section{
    padding: 1em 0;
    &--first{
      padding-top: 0;
    }
    &__top{
      margin-bottom: 1em;
    }
  }

  .grid-column3,
  .grid-column4,
  .grid-column5{
    gap: 1em;
  }

  .grid-column3,
  .grid-column4{
    grid-template-columns: repeat(2,calc(50% - .5em));
  }

  .grid-column5{
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
  }

  .tab-slider{
    overflow: unset;
    .swiper-container{
      &:before,
      &:after{
        display: none;
      }
    }
    .swiper-button{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .grid-column3,
  .grid-column4{
    grid-template-columns: 100%;
  }

  .grid-column5{
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
  }
}
