.header._open-catalog-menu{
  .menu{
    opacity: 1;
    pointer-events: unset;
    transform: translateX(0);
  }
}

.header._open-brand-menu{
  .brand-menu{
    opacity: 1;
    pointer-events: unset;
    transform: translateX(0);
  }
}

.header._open-sale-menu{
  .sale-menu{
    opacity: 1;
    pointer-events: unset;
    transform: translateX(0);
  }
}

.sale-menu,
.brand-menu,
.menu{
  margin-top: -1px;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background: #FFF;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 30px 66px 0px rgba(0, 0, 0, 0.10), 0px 120px 120px 0px rgba(0, 0, 0, 0.09), 0px 270px 162px 0px rgba(0, 0, 0, 0.05), 0px 480px 192px 0px rgba(0, 0, 0, 0.01), 0px 750px 210px 0px rgba(0, 0, 0, 0.00);
  z-index: 10;
}

.menu{
  &__inner{
    display: grid;
    grid-template-columns: 60em calc(100% - 60em);
  }
  &__items{
    position: relative;
  }
  &__banner{
    width: 20.0625em;
    max-width: 100%;
    padding: 2.4em 0;
    .sale-card{
      color: #07353A;
      &__img{
        padding-bottom: 139%;
      }
    }
  }
}

.menu-item{
  width: 21.6875em;
  @media screen and (min-width: 993px){
    &:hover{
      .menu-item__show{
        background-color: $darkBlue;
        color: #fff;
      }
      .menu-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show:hover,
    &:hover .menu-item__show{
      .menu-item__icon{
        path{
          stroke: #fff;
        }
      }
      .menu-item__arrow{
        path{
          fill: #fff;
        }
      }
      .menu-item__num{
        color: rgba(#fff,.7);
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    padding: .5em .75em;
    border-bottom: 1px solid #EDEFF1;
    min-height: 3.625em;
    transition: all .2s;
    cursor: pointer;
  }
  &__icon{
    width: 2.1875em;
    height: 2.1875em;
    @include center;
    margin-right: .625em;
    flex-shrink: 0;
    svg{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__text{
    display: flex;
    min-height: 2.1875em;
    padding: .375em 0;
  }
  &__title{
    font-size: .8125em;
    line-height: 154%;
    text-transform: uppercase;
    font-weight: 500;
  }
  &__num{
    margin-top: -.5em;
    font-size: .75em;
    line-height: 167%;
    color: #A7ACB0;
    margin-left: .5em;
  }
  &__arrow{
    margin-left: auto;
    width: .375em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    min-height: 100%;
    position: absolute;
    top: 0;
    left: 21.6875em;
    margin-left: -1px;
    width: 20.875em;
    opacity: 0;
    pointer-events: none;
    transition: all .2s;
    padding: .625em 0;
  }
}

.menu-item2{
  @media screen and (min-width: 993px){
    &:hover{
      .menu-item2__hidden{
        opacity: 1;
        pointer-events: unset;
      }
      .menu-item__show{
        background-color: $darkBlue;
        color: #fff;
      }
    }
    &:hover .menu-item2__show,
    &__show:hover{
      color: $darkBlue;
      .menu-item2__icon{
        path{
          stroke: $darkBlue;
        }
      }
      .menu-item__arrow{
        path{
          fill: $darkBlue;
        }
      }
    }
  }
  &__show{
    padding: .52em 2.875em;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  &__icon{
    width: 1.75em;
    height: 1.75em;
    @include center;
    margin-right: .94em;
    flex-shrink: 0;
    svg{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: .75em;
    line-height: 130%;
    text-transform: uppercase;
    font-weight: 400;
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: 20.6875em;
    margin-left: -1px;
    width: 17.4375em;
    opacity: 0;
    pointer-events: none;
    transition: all .2s;
    padding: .9375em 0;
  }
}

.menu3{
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.5em;
  }
  &__link{
    color: #28292A;
    @include lastM(0 0 1.5em);
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
}

.menu-top{
  display: none;
  &__close,
  &__back{
    position: relative;
    flex-shrink: 0;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 250%;
      height: 250%;
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__back{
    width: .875em;
    margin-right: .625em;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
    text-transform: uppercase;
  }
  &__close{
    margin-left: auto;
    width: 1.75em;
    padding-left: 1em;
  }
}

.brand-menu{
  padding: 1.75em 0 3.44em;
  &__inner{
    column-count: 7;
    column-gap: 1em;
  }
  &__group{
    display: inline-block;
    width: 100%;
    @include lastM(0 0 2.25em);
  }
  &__title{
    font-size: 1.375em;
    line-height: 132%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__links{
    margin-top: .5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-weight: 400;
    text-transform: uppercase;
    @include lastM(0 0 .5em);
    span{
      font-size: .75em;
      line-height: 130%;
    }
  }
}

.sale-menu{
  padding: 2.75em 0 3.25em;
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.875em;
  }
  &__link{
    width: 22.4375em;
    height: 3.5625em;
    font-weight: 400;
    border-radius: 0.25em;
  }
  .sale-card{
    position: relative;
    &:before{
      z-index: 2;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: .4375em;
      background: linear-gradient(0deg, rgba(7, 69, 131, 0.61) 0%, rgba(7, 69, 131, 0.61) 100%);
    }
    &__content{
      max-width: 25em;
      z-index: 3;
    }
  }
}


@media screen and (max-width: 992px){
  .menu-item2__hidden,
  .menu-item__hidden,
  .menu{
    overflow: hidden;
    height: $fullHeight;
    top: 0;
    left: 0;
    position: fixed;
  }

  .menu{
    opacity: 1;
    pointer-events: unset;
    transform: translateX(-120%);
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__banner{
      display: none;
    }
  }

  .menu-top{
    height: 4em;
    padding: 0 1.25em;
    display: flex;
    align-items: center;
  }

  .menu-scroll{
    overflow-y: auto;
    height: calc(100 * var(--vh) - 4em);
  }

  .menu-item{
    width: 100%;
    &._open-menu2{
      .menu-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      background-color: #fff;
      width: 100%;
      padding: 0;
    }
  }

  .menu-item2{
    &._open-menu3{
      .menu-item2__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show{
      padding: .81em .75em;
      border-bottom: 1px solid #EDEFF1;
    }
  }

  .menu-item2{
    width: 100%;
    &__hidden{
      background-color: #fff;
      width: 100%;
      padding: 0;
    }
  }

  .menu3{
    &__title{
      display: none;
    }
    &__links{
      margin: 0;
      padding: 0 1.25em;
    }
  }

  .sale-menu,
  .brand-menu{
    display: none;
  }
}
