.main-categories{
  padding: 2.25em 0 1em;
  @media screen and (min-width: 993px) {
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(10,calc(10% - (1.81em * 9) / 10));
        gap: 1.81em;
      }
    }
  }
}

.main{
  &__grid{
    margin-top: .875em;
    display: grid;
    grid-template-columns: calc(100% - 27.5em) 25.625em;
    gap: 1.875em;
  }
}

.main-banners{
  position: relative;
  overflow: hidden;
  border-radius: .5em;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-nav{
      position: absolute;
      left: 0;
      bottom: 1.875em;
      width: 100%;
      padding: 0 3.75em;
      display: flex;
      align-items: center;
    }
    &-button{
      position: static;
      margin: 0 .94em 0 0;
    }
    &-pagination{
      position: static;
      width: unset;
      margin-left: auto;
    }
  }
}

.banner{
  border-radius: .5em;
  min-height: 31.25em;
  padding: 6em 3.75em;
  background-size: cover;
  background-position: center;
  color: #fff;
  &__info{
    @include grid100gap(.75em);
    max-width: 32em;
    .text19{
      max-width: 24em;
    }
  }
  &__nav{
    display: flex;
    margin-top: .7em;
  }
  &__btn{
    padding: 0 1.85em;
    border-radius: .3125em;
    height: 4.25em;
    font-weight: 700;
    text-transform: uppercase;
    span{
      font-size: .8125em;
      letter-spacing: 0.0325em;
    }
    svg{
      width: .9375em;
      height: auto;
      margin-left: 1em;
    }
  }
}

.main-sales{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.popular-categories{
  &__grid{
    margin-top: 1.94em;
  }
}

.brands{
  .swiper{
    &-nav{
      display: flex;
      align-items: center;
    }
    &-button{
      position: static;
      width: .625em;
      height: auto;
      margin: 0 0 0 1.5em;
    }
  }
}

.brands-slider{
  overflow: hidden;
}

.collection{
  overflow: hidden;
  .tab-slider{
    margin-top: 1.125em;
  }
  .tab-content{
    margin-top: 1.875em;
  }
}

.product-slider,
.popular-slider{
  position: relative;
  &>.swiper-button{
    border: $border;
    &-prev{
      left: -4.5em;
    }
    &-next{
      right: -4.5em;
    }
  }
}

.product-slider{
  &>.swiper-container{
    width: calc(100% + 1.875em);
    & > .swiper-wrapper > .swiper-slide{
      width: 20%;
      padding-right: 1.875em;
      @media screen and (min-width: 993px){
        opacity: 0;
        pointer-events: none;
        transition: all .3s;
        &-active,
        &-next,
        &-next+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}

.popular{
  overflow: hidden;
}

.popular-slider{
  margin-top: 2.5em;
  .swiper{
    &-container{
      overflow: hidden;
      width: calc(100% + 2.5em);
    }
    &-slide{
      width: 50%;
      padding-right: 2.5em;
    }
  }
}

.info{
  padding: 3.88em 0 9em;
  background-color: #F2F6FA;
  background-image: url(../img/info-bg.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 13.25em auto;
  margin: 4.7em 0 5.19em;
  &__inner{
    position: relative;
  }
  &__text{
    margin-top: 1em;
    column-count: 2;
    column-gap: 2.69em;
    color: #484D53;
    padding-right: 3em;
    p{
      &:last-child{
        max-width: 49em;
      }
    }
  }
}

.advantages{
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin-top: 3.5em;
  border-radius: 0.3125rem;
  background: #FFF;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.05), -.125em .375em .875em 0 rgba(0, 0, 0, 0.05), -.5em 1.5em 1.625em 0 rgba(0, 0, 0, 0.04), -1.125em 3.4375em 2.1875em 0 rgba(0, 0, 0, 0.03), -2em 6.0625em 2.5em 0 rgba(0, 0, 0, 0.01), -3.1875em 9.5em 2.8125em 0 rgba(0, 0, 0, 0.00);
  padding: 2.5em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 8.2em * 3 / 4));
  justify-content: space-between;
  gap: 2em;
}

.advantage-card{
  display: flex;
  position: relative;
  &:not(:last-child){
    &:after{
      content: '';
      position: absolute;
      border-right: 1px dashed #D6DBE1;
      height: 100%;
      width: 1px;
      left: 100%;
      top: 0;
      margin-left: 4.1em;
    }
  }
  &__icon{
    width: 2.8125em;
    flex-shrink: 0;
    margin-right: 1.3125em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__content{
    max-width: 14.5em;
    @include grid100gap(.625em);
  }
  .text18{
    line-height: 122%;
  }
  .text15{
    color: #5B5D62;
  }
}

.viewed-products{
  margin-top: 2em;
  padding: 3.125em 0;
  &__grid{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .main{
    overflow: hidden;
  }

  .main-categories{
    .swiper{
      &-slide{
        width: 9.5em;
        padding-right: 1em;
      }
    }
  }

  .main{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .main-banners{
    .swiper{
      &-nav{
        display: none;
      }
    }
  }

  .banner{
    padding: 2.1875em;
    min-height: auto;
    &__btn{
      height: 3em;
      padding: 0 1.25em;
    }
  }

  .main-sales{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }

  .popular-categories{
    &__grid{
      margin-top: 1em;
    }
  }

  .brands{
    overflow: hidden;
    .swiper{
      &-nav{
        display: none;
      }
      &-slide{
        width: 18em;
        padding-right: 1em;
      }
    }
  }

  .brands-slider{
    overflow: unset;
  }

  .collection{
    .tab-slider{
      margin-top: 1em;
    }
    .tab-content{
      margin-top: 1.25em;
    }
  }

  .product-slider{
    & > .swiper-container > .swiper-wrapper{
      & > .swiper-slide{
        width: 19em;
        padding-right: 1em;
      }
    }
  }

  .popular-slider{
    margin-top: 1.25em;
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        width: 75%;
        padding-right: 1em;
      }
    }
  }

  .info{
    padding: 1em 0 0;
    margin: 1em 0;
    background-image: unset;
    &__text{
      column-count: 1;
      padding-right: 0;
      p{
        &:last-child{
          max-width: 100%;
        }
      }
    }
  }

  .advantages{
    position: static;
    width: calc(100% + 2.5em);
    margin: 2em 0 0 -1.25em;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 2em 1.5em;
    padding: 1.25em;
  }

  .advantage-card{
    &:after{
      display: none;
    }
    &__icon{
      margin-right: .25em;
    }
    &__content{
      gap: .25em;
    }
  }

  .viewed-products{
    margin-top: 0;
    padding: 1.5em 0;
    &__grid{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-sales{
    grid-template-columns: 100%;
  }

  .banner{
    padding: 1em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#000,.3);
    }
    &__info{
      position: relative;
      z-index: 2;
      gap: .5em;
    }
  }

  .brands{
    .swiper{
      &-slide{
        width: 14em;
      }
    }
  }

  .product-slider{
    & > .swiper-container > .swiper-wrapper{
      & > .swiper-slide{
        width: 17em;
        padding-right: 1em;
      }
    }
    .product-card{
      font-size: .875em;
    }
  }

  .popular-slider{
    .swiper{
      &-slide{
        width: 90%;
      }
    }
  }

  .advantages{
    grid-template-columns: 100%;
    gap: 1.5em;
  }

  .advantage-card{
    &__icon{
      width: 2em;
      margin-right: .75em;
    }
  }

  .viewed-products{
    &__grid{
      gap: .75em;
    }
  }
}
