.history{
  padding-bottom: 2.75em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    width: 15.8125em;
    height: 3.25em;
    font-weight: 400;
    border-radius: 0.25em;
  }
  &__groups{
    margin-top: 1.56em;
    @include grid100gap(1.37em);
    max-width: 80.625em;
  }
}

.history-group{
  border-radius: 0.3125em;
  border: 1px solid #E9EDF2;
  padding: 1.56em;
  &__title{
    font-size: 1em;
    font-weight: 600;
    text-transform: uppercase;
  }
}

.history-info{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &__item{
    margin: 1.125em 2.25em 0 0;
    display: flex;
    align-items: center;
  }
  &__text{
    font-size: .875em;
    line-height: 115%;
  }
  &__link{
    padding: 0 .625em;
    height: 2.125em;
    font-weight: 400;
    margin-left: .69em;
  }
}

.history-hint{
  margin-top: 1.125em;
  &__item{
    font-size: .875em;
    line-height: 171%;
    color: #2E3236;
  }
}

.orders{
  margin-top: 1.125em;
  .cart-card{
    grid-template-columns: 7.3125em calc(100% - 48.125em) 20em 11.875em 3.4375em;
    &__content{
      max-width: 23em;
    }
    &__price{
      @media screen and (min-width: 577px){
        grid-column: 4/6;
        text-align: right;
        align-items: flex-end;
      }
    }
  }
}

.order-sum{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1.125em;
  &__delivery,
  &__pay{
    display: flex;
    align-items: center;
  }
  &__delivery{
    &-title{
      font-size: .8125em;
      line-height: 170%;
    }
    &-num{
      font-size: 1.0625em;
      font-weight: 400;
      color: #000;
      margin-left: .7em;
    }
  }
  &__pay{
    &-title{
      font-size: 1.3125em;
      line-height: 105%;
    }
    &-num{
      margin-left: 2em;
      font-size: 1.75em;
      font-weight: 400;
    }
  }
}

.last-orders{
  margin-top: 2em;
  @include grid100gap(1.125em);
}

.order-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: .94em;
  &:not(:last-child){
    border-bottom: 1px solid #F1F3F4;
  }
  &__content{
    @include grid100gap(.5em);
  }
  &__title{
    font-size: 1.125em;
    font-weight: 500;
  }
  &__desc{
    font-size: .875em;
    line-height: 114%;
  }
  &__nav{
    margin-left: 1em;
    width: 27.3125em;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .47em));
    gap: .94em;
  }
  &__btn{
    width: 100%;
    height: 2.5em;
    font-weight: 400;
    &:hover{
      path{
        stroke: #fff;
      }
    }
    svg{
      margin-right: .625em;
      width: 1.125em;
      height: auto;
      flex-shrink: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .history{
    padding-bottom: 1em;
  }

  .orders{
    .cart-card{
      grid-template-columns: 4em calc(100% - 23.75em) 7.25em 7em 2.5em;
      padding-left: 1em;
    }
  }

  .order-card{
    display: block;
    &__nav{
      margin: 1em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .history{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      height: 2.75em;
      width: 12em;
      margin: .75em 0 0;
      span{
        font-size: .875em;
      }
    }
  }

  .history-group{
    padding: .75em;
  }

  .history-info{
    flex-direction: column;
    align-items: flex-start;
    &__item{
      flex-direction: column;
      align-items: flex-start;
      margin: .5em 0 0;
    }
    &__text{
      font-size: .75em;
    }
    &__link{
      margin: .5em 0 0;
    }
  }

  .history-hint{
    &__item{
      font-size: .75em;
    }
  }

  .orders{
    .cart-card{
      padding: .5em;
      grid-template-columns: 7em calc(100% - 7.75em);
      &__content{
        grid-column: 1/-1;
      }
      .product-card__price2{
        font-size: .8125em;
      }
    }
  }

  .order-sum{
    flex-direction: column;
    align-items: flex-end;
    &__pay{
      margin-top: .75em;
      &-title{
        font-size: .8125em;
      }
      &-num{
        font-size: 1.25em;
        margin-left: .75em;
      }
    }
  }

  .last-orders{
    margin-top: 1em;
  }

  .order-card{
    &__nav{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }
}
