.rating{
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: .875em;
    margin-right: .125em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $orange;
      }
      &~.rating-input__item {
        path{
          fill: $orange;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: $orange;
  }
}

.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    border: $border;
    padding: 0 1.07em;
    height: 4.42em;
    width: 100%;
    font-size: .875em;
    border-radius: .36em;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #979DA4;
    }
    &:focus {
      border-color: $darkBlue;
    }
  }
  textarea{
    height: 6.92em;
    resize: none;
  }
  label{
    transition: all .2s;
    font-size: .875em;
    line-height: 157%;
    display: block;
    margin-bottom: .5em;
  }
  &._required{
    label:after{
      content: ' *';
      color: $red;
    }
  }
  &__hint{
    position: absolute;
    left: 100%;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 2.0625em;
    max-width: 100%;
    width: 14em;
    color: #787C82;
    display: flex;
    align-items: flex-start;
    span{
      font-size: .875em;
      line-height: 135%;
    }
    svg{
      width: 1.1875em;
      height: auto;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  .nice-select{
    height: 2.6875em;
    width: 100%;
    border: $border;
    font-size: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.3125em;
    white-space: pre-wrap;
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .6875em;
      height: .75em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
    }
    .current{
      font-size: .875em;
      line-height: 157%;
      color: #58595A;
    }
    .list{
      margin-top: -.25em;
      border-radius: 0 0 0.3125em 0.3125em;
      width: calc(100% + 2px);
      left: -1px;
      border: $border;
      background: #FFF;
      box-shadow: none;
    }
    .option{
      width: calc(100% + 2px);
      height: 2.6875em;
      font-size: .875em;
      line-height: 157%;
      padding: 0 1.07em;
      display: flex;
      align-items: center;
      color: #58595A;
      border: none;
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $lightBlue;
        font-weight: 300;
        color: #fff;
      }
    }
  }
  &._big{
    .nice-select{
      height: 3.875em;
    }
    .option{
      height: 5.14em;
    }
  }
}

.fg-dynamic{
  textarea,
  input{
    padding-top: 1.5em;
    &._filled,
    &:focus{
      &~label{
        top: .5em;
        left: 1.27em;
        font-size: .75em;
      }
    }
  }
  label{
    position: absolute;
    top: 1.428em;
    left: 1.07em;
  }
}

.pass-toggle{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: .94em;
  width: 1.25em;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    path{
      stroke: #000;
    }
  }
}


.m-radio,
.m-switch,
.file-input,
.sort-item,
.filter-item,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    span{
      font-size: .875em;
      line-height: 129%;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
    }
  }
}

.m-check {
   input {
    &:checked+label {
      font-weight: 500;
      &::before {
        background-color: $darkBlue;
        border-color: $darkBlue;
        background-image: url(../img/check2.svg);
      }
    }
  }
  label {
    padding-left: 2em;
    padding-top: .09em;
    min-height: 1.3125em;
    color: #323638;
    &:before {
      border-radius: .1875em;
      border: 1px solid #E7EAEF;
      box-shadow: 0 .25em .625em 0 rgba($lightBlue,.08);
      width: 1.3125em;
      height: 1.3125em;
      @include bgImgSize(.6875em);
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border-color: $darkBlue;
    }
    &:after{
      opacity: 1;
    }
  }
  label{
    padding-left: 2.25em;
    padding-top: .3125em;
    min-height: 1.625em;
    &:before{
      width: 1.625em;
      height: 1.625em;
      border: $border;
      border-radius: 50%;
    }
    &:after{
      content: '';
      position: absolute;
      left: .4375em;
      top: .4375em;
      width: .75em;
      height: .75em;
      border-radius: 50%;
      background-color: $darkBlue;
      transition: all .1s;
      opacity: 0;
    }
  }
}

.cartcalc{
  display: grid;
  grid-template-columns: 1.5em calc(100% - 3em) 1.5em;
  border-radius: .3125em;
  border: 1px solid #ECEFF4;
  height: 3.3125em;
  input{
    text-align: center;
    border: none;
    border-left: 1px solid #ECEFF4;
    border-right: 1px solid #ECEFF4;
    font-size: 0.875em;
    line-height: 120%;
    font-weight: 400;
  }
  input,
  button{
    width: 100%;
    height: 100%;
  }
  svg{
    width: .5em;
    height: auto;
  }
  .ccalc-minus{
    svg{
      width: .4em;
    }
  }
  button{
    @include center;
    &:hover{
      svg{
        transform: scale(1.2);
      }
      path{
        fill: $darkBlue;
      }
    }
  }
}

.file-input{
  &__inner{
    padding: .84em;
    text-align: center;
    border-radius: 0.3125em;
    border: $border;
  }
  &__title,
  &__desc{
    font-size: .875em;
    line-height: 157%;
  }
  &__title{
    color: #757A80;
    font-weight: 500;
  }
  &__desc{
    color: #979DA4;
  }
}

.m-switch{
  input {
    &:checked+label{
      background: $darkBlue;
      &:before{
        background-color: #fff;
        box-shadow: 0 .125em .5em 0 rgba(0, 0, 0, 0.16);
        transform: translateX(1.1875em);
      }
    }
  }
  label{
    width: 2.625em;
    height: 1.4375em;
    border-radius: 1em;
    background-color: #D3D9E3;
    transition: all .3s;
    display: flex;
    align-items: center;
    padding: 0 .125em;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1.1875em;
      height: 1.1875em;
      background-color: $lightBlue;
      border-radius: 50%;
      transition: all .3s;
    }
  }
}
