.brands-page{
  padding-bottom: 2.5em;
  &__search{
    margin-top: 1.5em;
    max-width: 46.25em;
    input{
      height: 4em;
    }
  }
}

.countries{
  margin-top: 3.125em;
  &__grid{
    margin-top: 1.5em;
    max-width: 94.625em;
    column-count: 5;
    column-gap: 8.8em;
  }
}

.country-item{
  display: inline-flex;
  align-items: center;
  font-weight: 400;
  width: 100%;
  @include lastM(0 0 1.125em);
  span{
    font-size: 0.9375em;
    line-height: 147%;
  }
  img{
    width: 1.75em;
    flex-shrink: 0;
    margin-right: .625em;
  }
}

.brand-categories{
  margin-top: 4.75em;
  max-width: 94.625em;
  &__grid{
    column-count: 2;
    column-gap: 8.8em;
    width: 32.55em;
    max-width: 100%;
    margin-top: .81em;
  }
  &__link{
    display: inline-flex;
    width: 100%;
    text-decoration-line: underline;
    color: $darkBlue;
    span{
      font-size: .9375em;
      line-height: 173%;
    }
  }
}

.brand-alphabet{
  margin-top: 4.25em;
  &__items{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(28,calc(100% / 28));
    max-width: 90.75em;
  }
  &__item{
    height: 2.25em;
    @include center;
    border: 1px solid #EBEBEB;
    margin-left: -1px;
    margin-top: -1px;
    position: relative;
    transition: all .3s;
    cursor: pointer;
    &._active,
    &:hover{
      border-color: $darkBlue;
      z-index: 2;
    }
    @media screen and (min-width: 993px){
      &:nth-child(1),
      &:nth-child(28n - 27){
        margin-left: 0;
      }
      @for $i from 1 through 28 {
        &:nth-child(i) {
          margin-top: 0;
        }
      }
    }
  }
}

.brand-list{
  margin-top: 4.25em;
  &__groups{
    margin-top: 3.125em;
    @include grid100gap(4.25em);
  }
  &__letter{
    font-size: 4.125em;
    line-height: 33%;
    font-weight: 400;
  }
  &__items{
    margin-top: 2.3125em;
  }
  .brand-card{
    @media screen and (min-width: 993px){
      padding: 1em;
    }
  }
}



@media screen and (max-width: 992px){
  .brands-page{
    padding-bottom: 1em;
  }

  .brand-categories,
  .countries{
    margin-top: 2em;
    &__grid{
      column-count: 3;
      column-gap: 2em;
    }
  }

  .brand-categories{
    &__grid{
      width: 100%;
    }
  }

  .brand-alphabet{
    margin-top: 2em;
    &__items{
      margin-top: 1em;
      grid-template-columns: repeat(12,calc(100% / 12));
    }
    &__item{
      @media screen and (min-width: 577px){
        &:nth-child(1),
        &:nth-child(12n - 11){
          margin-left: 0;
        }
        @for $i from 1 through 12 {
          &:nth-child(i) {
            margin-top: 0;
          }
        }
      }
    }
  }

  .brand-list{
    margin-top: 2em;
    &__groups{
      margin-top: 1.25em;
      gap: 1em;
    }
    &__letter{
      font-size: 2em;
      line-height: 100%;
    }
    &__items{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .brand-categories,
  .countries{
    &__grid{
      column-count: 2;
      column-gap: 1em;
    }
  }

  .country-item{
    img{
      width: 1.5em;
      margin-right: .375em;
    }
    span{
      font-size: .875em;
    }
  }

  .brand-categories{
    &__link{
      span{
        font-size: .75em;
      }
    }
  }

  .brand-alphabet{
    &__items{
      grid-template-columns: repeat(7,calc(100% / 7));
    }
    &__item{
      height: 1.75em;
      span{
        font-size: .75em;
      }
      &:nth-child(1),
      &:nth-child(7n - 6){
        margin-left: 0;
      }
      @for $i from 1 through 7 {
        &:nth-child(i) {
          margin-top: 0;
        }
      }
    }
  }

  .brand-list{
    .brand-card{
      height: 2.75em;
      padding: .5em;
    }
    &__items{
      margin-top: .5em;
      grid-template-columns: repeat(3,calc(100% / 3 - .5em / 3));
      gap: .25em;
    }
  }
}
